const robotStore = {
  state: {
    dwGroupID: "未选择俱乐部",
    robotAry: [
      {
        index: "001",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "002",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "003",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "004",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "005",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "006",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "007",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "008",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "009",
        id: "163888",
        nickName: "蛋蛋"
      },
    ],
    robotCurrentIndex: 1
  },
  mutations: {
    changeRobotIndex(state, payload) {
      //做验证，去除不符合规则的页面
      if (payload < 1 || payload > Math.ceil(state.robotAry.length / 8)) {
        console.warn("错误的页面:", payload);
        return
      }
      state.robotCurrentIndex = payload;
    },
    toggleRobot(state, payload) {
      console.warn(payload);
      let index = state.robotAry.indexOf(payload);
      if (index >=0 && index < state.robotAry.length) {
        state.robotAry[index].isSelect = !state.robotAry[index].isSelect;
      }
    },
    //全选
    allRobot(state, payload) {
      state.robotAry.forEach(acp => {
        acp.isSelect = payload;
      })
    },
    reverseRobot(state, payload) {
      state.robotAry.forEach(acp => {
        acp.isSelect = !acp.isSelect;
      })
    },
    setClubID(state, payload) {
      state.dwGroupID = payload.dwGroupID;
    }
  },
  getters: {
    currentRobot(state) {
      return state.robotAry.slice((state.robotCurrentIndex - 1) * 8, (state.robotCurrentIndex) * 8);
    },
    totalRobotIndex(state) {
      return Math.ceil(state.robotAry.length / 8);
    }
  }
};

export {robotStore};



