import Vue from 'vue'

import Vuex from 'vuex'

import {accompanyStore} from './Accompany';
import {batchStore} from './Batch';
import {robotStore} from './Robot';
import {batchRobotStore} from './BatchRobotStore';
import {addStore} from './Add';
import {niuNiu} from './NiuNiu';
import {sanGong} from './SanGong';
import {ruleStore} from './Rule';
import {gameKind} from './GameKindStore';


Vue.use(Vuex);


//如何处理各个模块的页面
//1、总的totalArray
//2、当前的index,总的index(计算属性)
//3、删除:找到当前页面数组，当中要删除的项目，然后遍历总的数组删除该项目

function getIndex(index) {
  switch (true) {
    case index < 10 : {
      return "00" + index;
    }
    case index < 100 : {
      return "0" + index;
    }
    case index < 1000 : {
      return "" + index;
    }
  }
}

export default new Vuex.Store({
  state: {
    username: null,
    userInfo: null,
    groupList: [
      {
        index: '001',
        dwGroupID: 1268881,
        szGroupName: '测试俱乐部1',
        number: 66,
      },
      {
        index: '002',
        dwGroupID: 1268882,
        szGroupName: '测试俱乐部2',
        number: 66,
      },
      {
        index: '003',
        dwGroupID: 1268883,
        szGroupName: '测试俱乐部3',
        number: 66,
      },
      {
        index: '004',
        dwGroupID: 1268884,
        szGroupName: '测试俱乐部4',
        number: 66,
      },
      {
        index: '005',
        dwGroupID: 1268885,
        szGroupName: '测试俱乐部5',
        number: 66,
      },
      {
        index: '006',
        dwGroupID: 1268886,
        szGroupName: '测试俱乐部6',
        number: 66,
      },
      {
        index: '007',
        dwGroupID: 1268887,
        szGroupName: '测试俱乐部7',
        number: 66,
      },
      {
        index: '008',
        dwGroupID: 1268888,
        szGroupName: '测试俱乐部8',
        number: 66,
      },
      {
        index: '009',
        dwGroupID: 1268889,
        szGroupName: '测试俱乐部9',
        number: 66,
      },
      {
        index: '010',
        dwGroupID: 1268890,
        szGroupName: '测试俱乐部10',
        number: 66,
      }
    ],
    gourpCurrentIndex: 1,
    actionList: [],
    supplyList: [],
    debug: false,
    searchID: 0
  },
  mutations: {
    groupList(state, payload) {
      //添加序号
      payload.forEach((item, index) => {
        item.index = getIndex(index);
      });
      state.groupList = payload.concat();
    },
    changeGroupIndex(state, payload) {
      state.gourpCurrentIndex = payload;
    },
    toggleGroup(state, payload) {
      console.warn(payload);
      let index = state.groupList.indexOf(payload);
      if (index >=0 && index < state.groupList.length) {
        state.groupList[index].isSelect = !state.groupList[index].isSelect;
      }
    },
    ATemplateList(state, payload) {
      state.actionList = state.actionList.concat(payload);
    },
    STemplateList(state, payload) {
      state.supplyList = state.supplyList.concat(payload);
    },
    //设置用户state
    userInfo(state, payLoad) {
      console.log("设置账户信息:", payLoad);
      state.username = payLoad.szAccounts;
      state.userInfo = payLoad;
    },
    clear(state) {
      state.username = null;
      this.userInfo = null;
    }
  },
  getters: {
    getGroupById: (state) => (id) => {
      return state.groupList.find(item => item.dwGroupID === id);
    },
    currentGroup: state => {
      //搜索的功能,搜索时候如何处理index
      return state.groupList.slice((state.gourpCurrentIndex - 1) * 8, (state.gourpCurrentIndex) * 8);
    },
    totalGroupIndex: state => {
      //group总页面
      //如何约束当前页面使其小于等于总页面
      // if (Math.ceil(state.groupList.length / 8) < state.gourpCurrentIndex)
      return Math.ceil(state.groupList.length / 8);
    }
  },
  actions: {


  },
  modules: {
    accompany: accompanyStore,
    batch: batchStore,
    robot: robotStore,
    add: addStore,
    niu: niuNiu,
    san: sanGong,
    rule: ruleStore,
    batchRobot: batchRobotStore,
    kind: gameKind
  }
})














