<template>
  <div class="new-batch-wrapper">
    <el-dialog
      :visible.sync="onlyVisible"
      class="batch-wrapper"
      width="80%"

      custom-class="bbbbbbbb-wrapper"
    >
      <span slot="title" class="dialog-title">
        <div class="title">新建/管理批次</div>
        <div class="club-id txt item">俱乐部ID：<span class="content">{{$store.state.robot.dwGroupID}}</span></div>
        <div class="club-name txt item">俱乐部名称：<span class="content">{{getGroupName($store.state.robot.dwGroupID)}}</span></div>
        <div class="batch-id txt item">批次编号：<span class="content">{{batchID}}</span></div>
      </span>
      <div class="content-wrapper" >
        <div class="line"></div>
        <div class="show-list" >
          <table class="server-table" width="100%">
            <tr class="table-row table-title">
              <th class="table-title-item" width="25" style="flex-grow:1">      </th>
              <th class="table-title-item" width="70" style="flex-grow:1">序号</th>
              <th class="table-title-item" width="70" style="flex-grow:2">ID</th>
              <th class="table-title-item" width="70" style="flex-grow:3">昵称</th>
              <th class="table-title-item" width="70" style="flex-grow:3">玩家类型</th>
              <th class="table-title-item" width="70" style="flex-grow:2">游戏1</th>
              <th class="table-title-item" width="70" style="flex-grow:2">行为模式1</th>
              <th class="table-title-item" width="70" style="flex-grow:2">游戏2</th>
              <th class="table-title-item" width="70" style="flex-grow:2">行为模式2</th>
              <th class="table-title-item" width="70" style="flex-grow:2">游戏3</th>
              <th class="table-title-item" width="70" style="flex-grow:2">行为模式3</th>
              <th class="table-title-item" width="70" style="flex-grow:2">加分规则</th>
              <th class="table-title-item" width="70" style="flex-grow:4">服务时间</th>
              <th class="table-title-item" width="70" style="flex-grow:8"></th>
            </tr>
            <tr v-for="(item, index) in currentBatchRobot" class="table-row table-body">
              <td class="table-body-item" width="25"  style="flex-grow:1">
                <toggle-button class="check-input" :select="item.isSelect" :index="index" @toggle="toggleBtn"></toggle-button>
              </td>
              <td class="table-body-item" width="70"  style="flex-grow:1">{{item.index}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{item.dwUserID}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:3">{{getRobotNameByID(item.dwUserID)}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:3">{{}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{getKindName(item.wPlayKindID[0])}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{getTemplateName(item.dwActionTemplateID[0], item.wPlayKindID[0])}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{getKindName(item.wPlayKindID[1])}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{getTemplateName(item.dwActionTemplateID[1], item.wPlayKindID[1])}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{getKindName(item.wPlayKindID[2])}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{getTemplateName(item.dwActionTemplateID[2], item.wPlayKindID[2])}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{getRuleName(item.dwSupplyTemplateID)}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:4">{{item.serverTime}}</td>

              <td class="table-body-item table-item-operate" width="70"  style="flex-grow:8">
                <div height="36" class="table-btn-wrapper">
                  <my-button class="table-btn"  :commonSrc="editSrc" :targetIndex="index" eventName="editRobot" @editRobot="editRobot"></my-button>
                  <my-button class="table-btn" height="36" :commonSrc="deleteSrc" :targetIndex="index" eventName="deleteBatchRobot" @deleteBatchRobot="deleteBatchRobot"></my-button>
                </div>

              </td>
            </tr>
          </table>
        </div>
        <div class="btn-container">
          <div class="btn-wrapper">
            <div class="btn-all">
              <!--<input type="checkbox" @click="_cancel" v-model="btnCancel"><label >{{infoTip}}</label>-->
              <toggle-button class="check-input" :select="btnCancel"  @change="_cancel"></toggle-button>
              <span class="txt">全选</span>
              <toggle-button class="check-input" :select="btnReverse"  @change="_reverse"></toggle-button>
              <span class="txt">反选</span>
            </div>
            <div class="sub-wrapper">
              <!--批量修改属性按钮-->
              <my-button :commonSrc="modifyAllSrc" class="sub-button" event-name="modify-all" @modify-all="onModifyAll"></my-button>
              <my-button :commonSrc="putInSrc" class="sub-button" event-name="add-apy" @add-apy="addApyPage" :myWidth="112" :myHeight="36"></my-button>
            </div>

          </div>
        </div>
        <nav-footer class="nav-footer" :totalLength="$store.state.batchRobot.batchBatchRobotAry.length" :currentIndex="$store.state.batchRobot.batchRobotCurrentIndex" @changeIndex="_onChangePages"></nav-footer>
        <div class="btn-close">
          <my-button :commonSrc="agreeSrc" class="agree-btn" eventName="agree-event" @agree-event="addConfig"></my-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>

  import MyButton from '../myButton/MyButton.vue';
  import NavFooter from '../navFooter/NavFooter.vue';
  import ToggleButton from '../myToggleButton/MyToggleButton.vue';
  import {mapGetters} from 'vuex';

  import {FrameCtrl} from '../../frame/FrameCtrl'

  export default {
    data() {
      return {
        onlyVisible: this.dialogVisible,
        addSrc: "btn_add",
        editSrc: "btn_edit",
        deleteSrc: "btn_delete",
        deleteAllSrc: "btn_delete_all",
        agreeSrc: "btn_agree",
        modifyAllSrc: "btn_modify_all",
        putInSrc: "btn_put_in",
        infoTip: "全选",
        btnCancel: false,
        btnReverse: false,
        addApy: false,
        addType: 2,
        batchID: 0
      }
    },
    computed: {
      ...mapGetters(['currentBatchRobot', 'totalBatchRobotIndex', 'getKindName', 'getNiuTemplateName', 'getSanTemplateName', 'getRuleName', 'getGroupById', 'getSelectedBatchRobot','toggleBatchRobot', 'getRobotNameByID', 'getAllBatchRobotId'])
    },
    methods: {
      setBatchID(id) {
        this.batchID = id;
      },
      getGroupName(id) {
        if (id === undefined) return "";
        if (this.getGroupById(id)) {
          return this.getGroupById(id).szGroupName;
        } else {
          return "未获取到俱乐部名称";
        }
      },
      getTemplateName(id, kind) {
        switch (kind) {
          case 112: {
            return this.getNiuTemplateName(id);
          }
          case 79: {
            return this.getSanTemplateName(id);
          }
          default: {
            return "无";
          }
        }
      },
      addApyPage() {
        this.$emit('set-PutIn', this.getAllBatchRobotId);
        this.$emit('open-page', 3);
      },
      deleteBatchRobot(index) {
        console.log("删除批次成员", index);
        this.$confirm("确定删除该批次成员?").then(() => {
          let robot = this.currentBatchRobot[index];
          console.log(robot);
          let type = 4; //删除标识
          if (null === robot) return;
          if (this.$store.state.debug) {
            this.$store.commit('deleteBatchRobotAry', {
              dwBatchID: robot.dwBatchID,
              dwUserID: robot.dwUserID
            })
          } else {
            //删除批次成员
            FrameCtrl.getIns().modifyBatch(robot.dwUserID, type, {
              dwBatchID: robot.dwBatchID,
              dwUserID: robot.dwUserID
            })
          }
        }).catch((err) => {
          console.log(err)
        })
      },
      addConfig() {
        //发送添加批次
//        if (this.$store.state.debug === false) {
//          FrameCtrl.getIns().appendBatch(100002, 132121, 545612);
//        }
        this.closeDialog();
      },
      handleClose(done) {
        done();
      },
      _close() {
        console.log("关闭");
        this.$emit("closeDialog", 1);
      },
      closeDialog() {
        this.onlyVisible = false;
      },
      openDialog() {
        this.onlyVisible = true;
      },
      editRobot(index) {
        //传递编辑的数据
        let robot = this.currentBatchRobot[index];
        console.log(robot);
        this.$emit('set-current-robot', robot);
        //打开编辑页面
        this.$emit('open-page', 4);
      },
      deleteRobot() {},
      _cancel(index) {
        console.log("选中:", index);
        this.btnCancel = index;
        this.$store.commit('allBatchRobot', index);

      },
      _reverse(index) {
        console.log("选中:", index);
        this.btnReverse = index;
        this.$store.commit('reverseBatchRobot', index);
      },
      _addApy() {
        this.addApy = !this.addApy;
      },
      _close() {

      },
      onSelectType(obj) {
        this.addType = obj.type;
      },
      _onChangePages(index) {
        this.$store.commit("changeBatchRobotIndex", index);
      },
      toggleBtn(event) {
        let index = event.type;
        //如何找到state并且改变isSelect
        //如何根据type
        let target = this.currentBatchRobot[index];
        this.$store.commit('toggleBatchRobot', target);
      },
      onModifyAll() {
        //要将选中的信息导入的批量修改页面吗？程序需要知道，页面不需要
        let back = [];
        back = back.concat(this.getSelectedBatchRobot);
        if (back.length === 0) {
          this.$message({
            message: '请选择需要修改的批次',
            type: 'error'
          });
          return;
        } else {
          console.log(back);
          this.$emit('set-modify-id', back);
          this.$emit('open-page', 2);
        }
        //传入所有的信息

      }
    },
    props: {
      dialogVisible: {
        type: Boolean,
        default: false
      }
    },
    components: {
      'my-button': MyButton,
      'nav-footer': NavFooter,
      'toggle-button': ToggleButton
    }
  }
</script>

<style  scoped type="text/stylus" lang="stylus">
  @import "./stylus/newBatch.styl";
</style>



