    },
    methods: {
      descInput() {
        let txtVal = this.getByteLen(this.moduleRemarks);
        console.log(txtVal);
        if (txtVal > 128) {
          this.$message({
            type: "error",
            message: "备注最多128个字节"
          });
          let arr = this.moduleRemarks.split('');//　输入字符串转为数组，依次推出最后一位元素
          for (let i = arr.length - 1; i >= 0; i--) {
            arr = arr.slice(0, i);// 每推出一个，将数组转为字符串，做一次占位数判断
            this.moduleRemarks = arr.join('');
            if (this.getByteLen(this.moduleRemarks) <= 128) {
              break
            }
          }
        }
      },
      getByteLen(val) {
        let len = 0;
        for (let i = 0; i < val.length; i++) {
          let a = val.charAt(i);
          if (a.match(/[^\x00-\xff]/ig) != null) {
            len += 2;
          }
          else {
            len += 1;
          }
        }
        return len;
      },
      handleClose(done) {
        done();
      },
      _close() {
        console.log("关闭");
        this.$emit("closeRuleDialog");
      },
      closeDialog() {
        this.onlyVisible = false;
      },
      openDialog() {
        this.onlyVisible = true;
      },
      clearNoNum(e) {
        console.log(e);
        e.target.value = e.target.value.replace(/[^\d]/g,'');
        console.log(e.target.value, typeof e.target.value);
        console.log(parseInt(e.target.value));
        e.target.value = parseInt(e.target.value);
        if (parseInt(e.target.value) < 0) {
          e.target.value = 0;
        }
        if (parseInt(e.target.value) > 100) {
          console.log("进行限制");
          e.target.value = 100;
        }
      },
      addItem(mode, index) {
        let module = this.moduleData[mode];
        if (module.multiple.length >= 4) {
          this.$message({
            type: "warning",
            message: "最多添加四项"
          });
          return;
        }
        module.multiple.splice(index + 1, 0 ,0);
        module.rate.splice(index + 1, 0 ,0);
        module.minMaxAry.splice(index + 1, 0 ,{
          max: 0,
          min: 0
        });
      },
      reduceItem(mode, index) {
        let module = this.moduleData[mode];
        module.minMaxAry.splice(index, 1);
        module.multiple.splice(index, 1);
        module.rate.splice(index, 1);
      },
      extendTemplate() {
        let templateID = this.templateToExtend;
        console.error(templateID);
        if (templateID == 0) return;
        let template = this.getRuleTemplateByID(templateID);
        console.log("继承模板数据", template);
        this.setTemplateData(template);
      },
      setTemplateData(template) {
        if(template === null) return;
        //每一项的长度
        for (let n = 0; n < 4; n++) {
            let moduleData = this.moduleData[n];
            moduleData.multiple = [];
            moduleData.rate = [];
            moduleData.minMaxAry = [];

            let item = template.SupplyTemplateData['cbSupplyRule' + (n + 1)].ary;
            let len = template.SupplyTemplateData['cbSupplyRule' + (n + 1)].count;
            for (let m = 0; m < len; m++) {
              switch (n) {
                case 0:
                case 2: {
                  moduleData.multiple.push(item[m].wScoreMultiple);
                  moduleData.rate.push(item[m].cbIncreaseRate);
                  moduleData.minMaxAry.push({
                    min: item[m].lMinIncreaseScore,
                    max: item[m].lMaxIncreaseScore
                  });
                  break;
                }
                case 1: {
                  moduleData.multiple.push(item[m].wWaitTime);
                  moduleData.rate.push(item[m].cbIncreaseRate);
                  moduleData.minMaxAry.push({
                    min: item[m].lMinIncreaseScore,
                    max: item[m].lMaxIncreaseScore
                  });
                  break;
                }
                case 3: {
                  moduleData.multiple.push(item[m].wScoreMultiple);
                  moduleData.rate.push(item[m].cbReduceRate);
                  moduleData.minMaxAry.push({
                    min: item[m].lMinReduceScore,
                    max: item[m].lMaxReduceScore
                  });
                  break;
                }
              }


            }
        }
      },
      setCommitType(type, data) {
        //1、新增
        //2、修改
        this.commitType = type;
        if (type === 2) {
          this.moduleID = data.dwTemplateID;
          this.moduleName = data.szTemplateName;
          this.moduleRemarks = data.szTemplateRemarks;
          this.setTemplateData(data);
        } else {
          this.moduleID = 0;
          this.moduleName = "";
          this.moduleRemarks = "";
          for(let n = 0; n < 4; n++) {
            this.moduleData[n].multiple = CInitData[n].multiple.concat();
            this.moduleData[n].rate = CInitData[n].rate.concat();
            this.moduleData[n].minMaxAry = [];
            for(let m = 0; m < 4; m++) {
              this.moduleData[n].minMaxAry.push({
                min: 5000,
                max: 10000
              })
            }
          }
        }
      },
      onCommit() {
        let id = this.moduleID;
        let szTemplateName = this.moduleName;
        let szTemplateRemarks = this.moduleRemarks;
        let increaseAry = [];
        for (let n = 0; n < 3; n++) {
          if (n == 1) continue;
          let back = {
            count: this.moduleData[n].multiple.length,
            ary: []
          };
          for (let m = 0; m < this.moduleData[n].multiple.length; m++) {
            back.ary.push({
              wWaitTime: 0,
              wScoreMultiple: this.moduleData[n].multiple[m],
              cbIncreaseRate: this.moduleData[n].rate[m],
              lMinIncreaseScore: this.moduleData[n].minMaxAry[m].min,
              lMaxIncreaseScore: this.moduleData[n].minMaxAry[m].max
            })
          }
          increaseAry.push(back);
        }
        let increaseItem = {
          count: this.moduleData[1].multiple.length,
          ary: []
        };
        for (let m = 0; m < this.moduleData[1].multiple.length; m++) {
          increaseItem.ary.push({
            wWaitTime: this.moduleData[1].multiple[m],
            wScoreMultiple: 0,
            cbIncreaseRate: this.moduleData[1].rate[m],
            lMinIncreaseScore: this.moduleData[1].minMaxAry[m].min,
            lMaxIncreaseScore: this.moduleData[1].minMaxAry[m].max
          })
        }
        increaseAry.splice(1, 0, increaseItem);
        let reduceItem = {
          count: this.moduleData[3].multiple.length,
          ary: []
        };
        for (let m = 0; m < this.moduleData[3].multiple.length; m++) {
          reduceItem.ary.push({
            wScoreMultiple: this.moduleData[3].multiple[m],
            cbReduceRate: this.moduleData[3].rate[m],
            lMinReduceScore: this.moduleData[3].minMaxAry[m].min,
            lMaxReduceScore: this.moduleData[3].minMaxAry[m].max
          })
        }
        if (this.$store.state.debug === false) {
          console.log(szTemplateName, szTemplateRemarks, increaseAry, reduceItem);
          if (this.commitType === 1) {
            FrameCtrl.getIns().appendSTemplate(szTemplateName, szTemplateRemarks, increaseAry, reduceItem);
          } else {
            FrameCtrl.getIns().modifySTemplate(szTemplateName, szTemplateRemarks, increaseAry, reduceItem);
          }
        } else {
          console.log(szTemplateName, szTemplateRemarks, increaseAry, reduceItem);
        }
        this._close();
      }
    },
    computed: {
      totalTemplate() {
        return this.$store.state.rule.ruleAry;
      },
      ...mapGetters(['getRuleTemplateByID'])
    },
    props: {
      ruleDialog: {
