import Vue from 'vue';
const tagTemplateA = {
  lScoreArea1: [],						//底分范围
  cbModeRate1: [],						//模式概率
  lScoreArea2: [],						//底分范围
  cbModeRate2: [],						//模式概率
  lScoreArea3: [],						//底分范围
  cbModeRate3: [],						//模式概率
  lScoreArea4: [],						//底分范围
  cbModeRate4: [],						//模式概率
  cbModeData1: [],					  //模式数据
  cbModeData2: [],					  //模式数据
  cbModeData3: [],					  //模式数据
	cbModeData4: [],
};
function getObj(obj) {
  let copy;
  copy = JSON.parse(JSON.stringify(obj));
  return copy;
}
const niuNiu = {
  state: {
    niuAry: [
      {
        dwTemplateID: 150,
        wGameKindID: 112,
        szTemplateName: "牛牛1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 151,
        wGameKindID: 112,
        szTemplateName: "赌圣1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 152,
        wGameKindID: 112,
        szTemplateName: "赌王1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 153,
        wGameKindID: 112,
        szTemplateName: "赌尊1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 154,
        wGameKindID: 112,
        szTemplateName: "赌帝1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 155,
        wGameKindID: 112,
        szTemplateName: "赌皇1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 156,
        wGameKindID: 112,
        szTemplateName: "牛牛1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 157,
        wGameKindID: 112,
        szTemplateName: "牛牛1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 158,
        wGameKindID: 112,
        szTemplateName: "牛牛1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 159,
        wGameKindID: 112,
        szTemplateName: "牛牛1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
    ],
    niuCurrentIndex: 1
  },
  mutations: {
    //牛牛数组push
    pushNiuAry(state, payload) {
      //可以验证数据结构;
      payload.forEach(item => {
        item.isSelect = false;
      });
      state.niuAry = payload;
    },
    addNiuAry(state, payload) {
      //判断是新增还是修改
      let niuIndex = -1;
      state.niuAry.forEach((item, index) => {
        if (item.dwTemplateID === payload.dwTemplateID) {
          niuIndex = index;
        }
      });
      if (niuIndex === -1) {
        payload.isSelect = false;
        state.niuAry.push(payload);
      } else {
        // state.niuAry.splice(niuIndex)
        payload.isSelect = false;
        state.niuAry.splice(niuIndex, 1);
        state.niuAry.splice(niuIndex, 0, payload);
        //state.niuAry[niuIndex] = {...payload};
        console.error("牛牛修改后的数据结构:", payload);
      }

    },
    deleteNiuModule(state, payload) {
      let deleteIndex = -1;
      state.niuAry.forEach((niu, index) => {
        if (niu.dwTemplateID === payload.dwTemplateID) {
          deleteIndex = index;
        }
      });
      if (deleteIndex !== -1) {
        state.niuAry.splice(deleteIndex, 1);
      }
    },
    changeNiuIndex(state, payload) {
      //做验证，去除不符合规则的页面
      if (payload < 1 || payload > Math.ceil(state.niuAry.length / 8)) {
        console.warn("错误的页面:", payload);
        return
      }
      state.niuCurrentIndex = payload;
    },
    toggleNiu(state, payload) {
      console.warn(payload);
      let index = state.niuAry.indexOf(payload);
      if (index >=0 && index < state.niuAry.length) {
        state.niuAry[index].isSelect = !state.niuAry[index].isSelect;
      }
    },
    //全选
    allNiu(state, payload) {
      state.niuAry.forEach(acp => {
        acp.isSelect = payload;
      })
    },
    reverseNiu(state, payload) {
      state.niuAry.forEach(acp => {
        acp.isSelect = !acp.isSelect;
      })
    }
  },
  getters: {
    currentNiu(state) {
      return state.niuAry.slice((state.niuCurrentIndex - 1) * 8, (state.niuCurrentIndex) * 8);
    },
    totalNiuIndex(state) {
      return Math.ceil(state.niuAry.length / 8);
    },
    getNiuTemplateName(state) {
      return id => {
        let module = state.niuAry.find(item => item.dwTemplateID == id);
        if (module != null) {
          return module.szTemplateName
        }
      }
    },
    getNiuTemplateByID(state) {
      return id => {
        let module = state.niuAry.find(item => item.dwTemplateID == id);
        return module
      }
    }
  }
}

export {niuNiu};



