<template>
  <!--:close-on-click-modal="false == true"-->
    <div class="accompany-wrapper">
      <el-dialog
        title="编辑陪玩"
        :visible.sync="onlyVisible"
        class="d-wrapper"
        >
        <div class="content-wrapper">
          <div class="line"></div>
          <div class="split">基本信息</div>
          <div class="base-info">
            <div class="avatar">avatar</div>
            <div class="info">
              <div class="number">
                <span class="txt">序号: </span><span class="index">{{userInfo.index}}</span>
                <span class="txt">ID: </span><span class="id">{{userInfo.dwUserID}}</span>
              </div>
              <div class="nick-name">
                <span class="txt">昵称:</span>
                <input type="text" class="name" readonly="readonly"  :value="nickName"/>
              </div>
              <div class="other">
                <div class="sex">
                  <span class="txt">性别:</span>
                  <div class="sex-radio">
                    <!--<input type="radio" name="sex"/>-->
                    <label>男</label>
                    <!--<label><input type="radio" name="sex"/> 女</label>-->
                  </div>
                </div>
                <div class="extend">
                  <span class="txt">选择继承对象:</span>
                  <!--<input type="text" class="extend-input">-->
                  <select class="extend-input" v-model="robotToExtend">
                    <option value="0">无</option>
                    <option v-for="item in $store.state.batchRobot.batchBatchRobotAry" :value="item.dwUserID">{{item.nickName}}</option>
                  </select>
                  <my-button :commonSrc="extendSrc" class="btn-extend" event-name="extendRobot" @extendRobot="extendRobot"></my-button>
                </div>
              </div>
            </div>
          </div>
          <div class="split">陪玩属性</div>
          <div class="select-wrapper">
            <div class="title"><span>游戏类型1:</span></div>
            <select v-model="gameKind1">
              <option value="0">无</option>
              <option v-for="(item, index) in $store.state.kind.kindAry" :value="item.wKindID">{{item.szKindName}}</option>
            </select >
            <div class="title"><span>行为模型1:</span></div>
            <select v-model="actionTemplateKind1" class="template-select">
              <!--根据游戏选择不同的选择模板-->
              <option value="0">无</option>
              <option v-for="(item) in getActionTemplateByID(gameKind1)" :value="item.dwTemplateID">{{item.szTemplateName}}</option>
            </select>
          </div>
          <div class="select-wrapper">
            <div class="title"><span>游戏类型2:</span></div>
            <select v-model="gameKind2">
              <option value="0">无</option>
              <option v-for="(item, index) in $store.state.kind.kindAry" :value="item.wKindID">{{item.szKindName}}</option>
            </select>
            <div class="title"><span>行为模型2:</span></div>
            <select v-model="actionTemplateKind2" class="template-select">
              <option value="0">无</option>
              <option v-for="(item) in getActionTemplateByID(gameKind2)" :value="item.dwTemplateID">{{item.szTemplateName}}</option>
            </select>
          </div>
          <div class="select-wrapper">
            <div class="title"><span>游戏类型3:</span></div>
            <select v-model="gameKind3">
              <option value="0">无</option>
              <option v-for="(item, index) in $store.state.kind.kindAry" :value="item.wKindID">{{item.szKindName}}</option>
            </select>
            <div class="title"><span>行为模型3:</span></div>
            <select v-model="actionTemplateKind3" class="template-select">
              <option value="0">无</option>
              <option v-for="(item) in getActionTemplateByID(gameKind3)" :value="item.dwTemplateID">{{item.szTemplateName}}</option>
            </select>
          </div>
          <div class="select-wrapper">
            <!--:picker-options="{-->
            <!--selectableRange: '18:30:00 - 20:30:00'-->
            <!--}"-->
            <div class="title"><span>上线时间:</span></div>
            <el-time-picker
              v-model="timeOnline"

              class="timeSelect"
              placeholder="任意时间点">
            </el-time-picker>
            <div class="title"><span>下线时间:</span></div>
            <el-time-picker
              v-model="timeOffline"

              class="timeSelect"
              placeholder="任意时间点">
            </el-time-picker>
          </div>
          <div class="select-wrapper">
            <div class="title"><span>俱乐部ID:</span></div>
            <input type="text" class="club-id" :value="$store.state.robot.dwGroupID" readonly>
          </div>
          <div class="select-wrapper">
            <div class="title"><span>加分规则:</span></div>
            <select v-model="gameRule" class="template-select">
              <option value="0">无</option>
              <option v-for="(item, index) in $store.state.rule.ruleAry" :value="item.dwTemplateID">{{item.szTemplateName}}</option>
            </select>
          </div>
          <div class="btn-close">
            <my-button :commonSrc="agreeSrc" class="agree-btn" eventName="agree-event" @agree-event="modifyRobot"></my-button>
            <my-button :commonSrc="closeSrc" class="close-btn" eventName="close-event" @close-event="_close"></my-button>
          </div>
        </div>


      </el-dialog>
    </div>
</template>

<script>

  function timeToString(time) {
    const date = new Date(time * 1000);
    return date.toTimeString().substr(0, 8);
  }

  function getServerTime(enter, leave) {
    return timeToString(enter) + " - " + timeToString(leave);
  }
  import MyButton from "../myButton/MyButton.vue";

  import {mapGetters} from 'vuex';
  import {FrameCtrl} from '../../frame/FrameCtrl';

  export default {
    data() {
      return {
        onlyVisible: this.dialogVisible,
        nickName: "",
        extendSrc: "btn_extend",
        timeOnline: this.userInfo.dwEnterTime * 1000,
        timeOffline: this.userInfo.dwLeaveTime * 1000,
        clubId: 123129,
        agreeSrc: "btn_agree",
        closeSrc: "btn_close",
        gameKind1: 0,
        actionTemplateKind1: 0,
        gameKind2: 0,
        actionTemplateKind2: 0,
        gameKind3: 0,
        actionTemplateKind3: 0,
        gameRule: 0,
        robotToExtend: 0
      }
    },
    methods: {
      handleClose(done) {
            done();
      },
      _close() {
        this.onlyVisible = false;
      },
      closeDialog() {
        this.onlyVisible = false;
      },
      openDialog() {
        this.onlyVisible = true;
      },
      extendRobot() {
        let self = this;
        let extend = this.getBatchRobotByID(this.robotToExtend);
        if (extend) {
          extend.wPlayKindID.forEach((id, index) => {
            this["gameKind" + (index + 1)] = id;
          });

          this.timeOnline = extend.dwEnterTime * 1000;
          this.timeOffline = extend.dwLeaveTime * 1000;
          this.gameRule = extend.dwSupplyTemplateID;

          this.$nextTick(() => {
            extend.dwActionTemplateID.forEach((id, index) => {
              self["actionTemplateKind" + (index + 1)] = id;
            });
          })
        }
      },
      resetInfo(robot) {
        let self = this;
        let extend = robot;
        if (extend) {
          //如何获得机器人名字
          this.nickName = this.getRobotNameByID(extend.dwUserID);
          extend.wPlayKindID.forEach((id, index) => {
            this["gameKind" + (index + 1)] = id;
          });

          this.timeOnline = extend.dwEnterTime * 1000;
          this.timeOffline = extend.dwLeaveTime * 1000;
          this.gameRule = extend.dwSupplyTemplateID;

          this.$nextTick(() => {
            extend.dwActionTemplateID.forEach((id, index) => {
              self["actionTemplateKind" + (index + 1)] = id;
            });
          })
        }
      },
      modifyRobot() {
        //集成所有信息
        let id = this.userInfo.dwBatchID;
        console.log(id);
        if (id == undefined) return;
        let type = 3;
        let kind = [this.gameKind1, this.gameKind2, this.gameKind3];
        let action = [this.actionTemplateKind1, this.actionTemplateKind2, this.actionTemplateKind3];
        let data = {
          dwBatchID: id,
          dwUserID: this.userInfo.dwUserID,
          dwEnterTime: this.timeOnline / 1000,
          dwLeaveTime: this.timeOffline / 1000,
          wPlayKindID: kind,
          dwSupplyTemplateID: this.gameRule,
          dwActionTemplateID: action
        };
        //console.log(data);
        if (this.$store.state.debug === false ) {
          console.log(id, type, data);
          FrameCtrl.getIns().modifyBatch(id, type, data);
        } else {
          this.$store.commit('pushBatchRobotAry', data);
        }
        this.$emit('open-page', 1);
      }
    },
    computed: {
      ...mapGetters(['getBatchRobotByID', 'getRobotNameByID']),
      getActionTemplateByID() {
        let self = this;
        return id => {
          switch (id) {
            case 112: {
              return self.$store.state.niu.niuAry;
            }
            case 79: {
              return self.$store.state.san.sanAry;
            }
          }
        }
      }
    },
    watch: {
      gameKind1(newValue, oldValve) {
        this.actionTemplateKind1 = 0;
      },
      gameKind2(newValue, oldValve) {
        this.actionTemplateKind2 = 0;
      },
      gameKind3(newValue, oldValve) {
        this.actionTemplateKind3 = 0;
      }
    },
    props: {
      dialogVisible: {
        type: Boolean,
        default: false
      },
      userInfo: {
        type: Object,
        default() {
          return {
            index: 0,
            dwUserID: 0,
            dwBatchID: 0,
            dwEnterTime: 0,
            dwLeaveTime: 0,
            wPlayKindID: [0, 0, 0],
            dwSupplyTemplateID: 0,
            dwActionTemplateID: [0, 0, 0],
            serverTime: getServerTime(0, 0) ,
            id: "163888",
            nickName: "蛋蛋"
          }
        }
      }
    },
    components: {
      'my-button': MyButton
    }
  }
</script>

<style  scoped type="text/stylus" lang="stylus">
  @import "./stylus/accompony.styl";
</style>



