var EventDispatcher = /** @class */ (function () {
    function EventDispatcher() {
        this._poor = {};
    }
    EventDispatcher.prototype.addEventListener = function (name, callBack, thisObj) {
        var listener = {
            thisObj: thisObj,
            callBack: callBack
        };
        if (!this._poor || this._poor[name] == null) {
            this._poor[name] = [];
        }
        this._poor[name].push(listener);
    };
    EventDispatcher.prototype.removeEventListener = function (name, callBack, thisObj) {
        if (null == this._poor[name] || this._poor[name].length == 0) {
            console.log("移除事件出错", name);
            return;
        }
        var index = -1;
        this._poor[name].forEach(function (item, itemIndex) {
            if (item.thisObj === thisObj && item.callBack === callBack) {
                index = itemIndex;
            }
        });
        if (index < 0) {
            console.log("未找到事件监听:", name);
            return;
        }
        this._poor[name].splice(index, 1);
        console.log("移除事件监听成功:", name);
    };
    //所有的监听器执行代码
    EventDispatcher.prototype.dispatchEvent = function (event) {
        //包括事件的名字以及事件的data
        var type = event.type;
        this._poor[type].forEach(function (item, index) {
            item.callBack.call(item.thisObj, event);
        });
    };
    return EventDispatcher;
}());
export { EventDispatcher };
