<template>
    <div class="batch-manager-wrapper">
      <div class="header">
        <div class="txt">
          <div class="club-wrapper">
            <span class="left">俱乐部ID：</span>{{$store.state.robot.dwGroupID}}
          </div>
          <div class="club-wrapper">
            <span class="left">俱乐部名称：</span>{{getGroupName($store.state.robot.dwGroupID)}}
          </div>
        </div>


        <div class="buttons">
          <my-button class="btn-build main-button" :commonSrc="addSrc" event-name="addApy" @addApy="_addApy"></my-button>
        </div>
      </div>

      <div class="show-list">
        <table class="server-table" width="100%">
          <tr class="table-row table-title">
            <th class="table-title-item" width="25" style="flex-grow:1">      </th>
            <th class="table-title-item" width="70" style="flex-grow:1">批次ID</th>
            <th class="table-title-item" width="70" style="flex-grow:2">俱乐部ID</th>
            <th class="table-title-item" width="70" style="flex-grow:3">昵称</th>
            <th class="table-title-item" width="70" style="flex-grow:2">人数</th>
            <th class="table-title-item" width="70" style="flex-grow:2">服务时间</th>
            <th class="table-title-item" width="70" style="flex-grow:8"></th>
          </tr>
          <tr v-for="(item, index) in currentBatch" class="table-row table-body">
            <td class="table-body-item" width="25"  style="flex-grow:1">

              <toggle-button class="check-input" :select="item.isSelect" :index="index" @toggle="toggleBtn"></toggle-button>
            </td>
            <td class="table-body-item" width="70"  style="flex-grow:1">{{item.dwBatchID}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{item.dwGroupID}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:3">{{getName(item.dwGroupID)}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{item.count}}</td>
            <td class="table-body-item blue-txt" width="70"  style="flex-grow:2">{{item.serverTime}}</td>

            <td class="table-body-item table-item-operate" width="70"  style="flex-grow:8">
              <div height="36" class="table-btn-wrapper">
                <my-button class="table-btn"  :commonSrc="editSrc" :targetIndex="index" eventName="editBatch" @editBatch="editBatch"></my-button>
                <my-button class="table-btn" height="36" :commonSrc="deleteSrc" :targetIndex="index" eventName="deleteBatch" @deleteBatch="deleteBatch"></my-button>
              </div>

            </td>
          </tr>
        </table>
      </div>

      <div class="btn-container">
        <div class="btn-wrapper">
          <div class="btn-all">
            <toggle-button class="check-input" :select="btnCancel"  @change="_cancel"></toggle-button>
            <span class="txt">全选</span>
          </div>
          <div class="sub-wrapper">
            <my-button :commonSrc="deleteAllSrc" class="sub-button" @click.native.stop="deleteSelectedBatch"></my-button>
          </div>

        </div>
      </div>
      <nav-footer class="nav-footer" :totalLength="$store.state.batch.batchAry.length" :currentIndex="$store.state.batch.batchCurrentIndex" @changeIndex="_onChangePages"></nav-footer>

      <div class="new-build-wrapper">
        <el-dialog
          title="新建批次"
          :visible.sync="newBatchPage"
          width="40%"
          class="info-wrapper"
          top="30vh"
        >
          <div class="content-wrapper">
            <div class="line"></div>
            <div class="split">基本信息</div>
            <div class="select-wrapper">
              <!--:picker-options="{-->
              <!--selectableRange: '18:30:00 - 20:30:00'-->
              <!--}"-->
              <div class="title"><span>上线时间:</span></div>
              <el-time-picker
                v-model="newTimeOnline"

                class="timeSelect"
                placeholder="任意时间点">
              </el-time-picker>
              <div class="title"><span>下线时间:</span></div>
              <el-time-picker
                v-model="newTimeOffline"

                class="timeSelect"
                placeholder="任意时间点">
              </el-time-picker>
            </div>
            <div class="btn-close">
              <my-button :commonSrc="agreeSrc" class="agree-btn" eventName="agree-event" @agree-event="addNewBatch"></my-button>
            </div>
          </div>
        </el-dialog>
      </div>


      <new-built-dialog :dialogVisible="builtDialog" @closeDialog="closeDialog" ref="newBuildDialog" @open-page="onTogglePage" @set-current-robot="setCurrentRobot" @set-modify-id="setModifyBatches" @set-PutIn="setPunIn"></new-built-dialog>
      <modify-dialog :modifyDialog="batchAddDialog" @closeDialog="closeDialog" ref="modifyDialog" @open-page="onTogglePage"></modify-dialog>
      <put-in-dialog :dialogVisible="putInDialog" @closeDialog="closeDialog" ref="putInDialog" @open-page="onTogglePage"></put-in-dialog>
      <accompony :dialogVisible="accomponyDialog" @closeDialog="closeDialog" ref="accomponyDialog" @open-page="onTogglePage" :userInfo="currentRobotInfo"></accompony>

    </div>
</template>

<script>

  function timeToString(time) {
    const date = new Date(time * 1000);
    return date.toTimeString().substr(0, 8);
  }

  function getServerTime(enter, leave) {
    return timeToString(enter) + " - " + timeToString(leave);
  }

  import MyButton from '../myButton/MyButton.vue';
  import NavFooter from '../navFooter/NavFooter.vue';
  import ToggleButton from '../myToggleButton/MyToggleButton.vue';
  import {mapGetters} from 'vuex';


  import NewBatchDialog from './NewBatch.vue';
  import ModifyDialog from "../modifyAll/ModifyAll.vue";
  import PutInDialog from './PutIn.vue';
  //陪玩页面
  import Accompony from '../accompony/Accompony.vue';

  import {FrameCtrl} from '../../frame/FrameCtrl';

  const CBatchEditDialog = 1;
  const CPutInDialog = 3;

  const InitUserInfo = {

      index: 0,
      dwUserID: 0,
      dwBatchID: 0,
      dwEnterTime: 132121,
      dwLeaveTime: 545612,
      wPlayKindID: [0, 0, 0],
      dwSupplyTemplateID: 0,
      dwActionTemplateID: [0, 0, 0],
      serverTime: getServerTime(0, 0) ,

      id: "0",
      nickName: "未传入"

  }

  export default {
    data() {
      return {
        //记得换成新建批次
        addSrc: "btn_new_batch",
        editSrc: "btn_edit",
        deleteSrc: "btn_delete",
        deleteAllSrc: "btn_delete_all",
        agreeSrc: "btn_agree",
        btnCancel: false,
        addApy: false,
        addType: 2,
        //dialog
        builtDialog: false,
        batchAddDialog: false,
        putInDialog: false,
        accomponyDialog: false,
        newBatchPage: false,
        //当前编辑的批次页面
        currentBatchInfo: null,
        currentRobotInfo: InitUserInfo,
        modifyBatches: [],
        //新建批次时间
        newTimeOnline: new Date(),
        newTimeOffline: new Date(),
      }
    },
    created() {

    },
    computed: {
      ...mapGetters(['currentBatch', 'totalBatchIndex', 'getGroupById'])
    },
    methods: {
      getName(id) {
        if (id === undefined) return "";
        if (this.getGroupById(id)) {
          return this.getGroupById(id).szGroupName;
        } else {
          return "未获取到俱乐部名称";
        }

      },
      getGroupName(id) {
        if (id === undefined) return "";
        if (this.getGroupById(id)) {
          return this.getGroupById(id).szGroupName;
        } else {
          return "未获取到俱乐部名称";
        }
      },
      editBatch(index) {
        //将信息导入编辑页面
        this.currentBatchInfo = this.currentBatch[index];
        if (this.currentBatchInfo.dwBatchID == null) {
          console.error("错误的批次ID:", this.currentBatchInfo.dwBatchID);
          return;
        }
        //打开编辑页面作为回调
        if (this.$store.state.debug === false) {
          //设置加载页面的回调
//          FrameCtrl.getIns().setLoadBatchMemberCall(this._openBatchEditDialog, this);
          //清空
          this.$store.commit('setBatchRobotAry', []);
          FrameCtrl.getIns().loadBatchMember(this.currentBatchInfo.dwBatchID);
          this._openBatchEditDialog();
        } else {
          this._openBatchEditDialog();
        }

      },
      _openBatchEditDialog() {
        let dialog = this._getDialog(CBatchEditDialog);
        let putIn = this._getDialog(CPutInDialog);
        putIn.setBatchID(this.currentBatchInfo.dwBatchID);
        dialog.setBatchID(this.currentBatchInfo.dwBatchID);
        dialog.openDialog();
      },
      deleteBatch(index) {
        this.$confirm("确定删除该批次?").then(() => {
          let deleteId = this.currentBatch[index].dwBatchID;
          if (deleteId === null) return;
          if (this.$store.state.debug === true) {
            console.log('成功删除批次:', deleteId);
            this.$store.commit('deleteBatchAry', {
              dwBatchID: deleteId
            })
          } else {
            FrameCtrl.getIns().deleteBatch(deleteId);
          }
        }).catch(err => {
          console.warn("取消删除批次:", err);
        })
      },
      /**
       * 删除所选的批次
       * */
      deleteSelectedBatch() {
        this.$confirm("确定删除所有已选的批次吗?").then(() => {
          let batchesID = [];
          this.$store.state.batch.batchAry.forEach(item => {
            if (item.isSelect === true) {
              batchesID.push(item.dwBatchID);
            }
          });
          batchesID.forEach(id => {
            if (this.$store.state.debug === true) {
              this.$store.commit('deleteBatchAry', {
                dwBatchID: id
              });
            } else {
              FrameCtrl.getIns().deleteBatch(id);
            }
          })
        }).catch(err => {

        })

      },
      _cancel(index) {
        this.btnCancel = index;
        this.$store.commit('allBatch', index);
      },
      _addApy() {
        let id = this.getGroupById(this.$store.state.robot.dwGroupID);
        if (id == null) {
          this.$confirm("请先选择需要编辑的俱乐部.", "错误", () => {

          }).catch(err => {
            console.log(err);
          });
          return
        }
        this.newBatchPage = true;
//        this.addApy = !this.addApy;
        //新建批次按钮
//        console.log("新建批次");
        //this.batchAddDialog = true;
//        let newBuilt = this._getDialog(1);
//        newBuilt.openDialog();
      },
      _close() {

      },
      onSelectType(obj) {
        this.addType = obj.type;
      },
      _onChangePages(index) {
        this.$store.commit("changeBatchIndex", index);
      },
      toggleBtn(event) {
        let index = event.type;
        //如何找到state并且改变isSelect
        //如何根据type
        let target = this.currentBatch[index];
        this.$store.commit('toggleBatch', target);
      },
      onTogglePage(index) {
        this._closeAllDialog();
        let dialog = this._getDialog(index);
        dialog.openDialog();
      },
      closeDialog(index) {
//        console.log("关闭页面:", index);
        let dialog = this._getDialog(index);
        dialog.closeDialog();
      },
      _getDialog(index) {
        let dialog;
        switch (index) {
          case 1 : {
            dialog = this.$refs.newBuildDialog;
            break;
          }
          case 2 : {
            dialog = this.$refs.modifyDialog;
            break;
          }
          case 3 : {
            //查找该批次下所有机器人的ID
            dialog = this.$refs.putInDialog;
            break;
          }
          case 4 : {
            dialog = this.$refs.accomponyDialog;
            break;
          }
        }
        return dialog;
      },
      _closeAllDialog() {
        for (let n = 1; n <= 4; n++ ) {
          this.closeDialog(n);
        }
      },
      setCurrentRobot(robot) {
        this.currentRobotInfo = robot;
        let dialog = this._getDialog(4);
        dialog.resetInfo(robot);
      },
      setModifyBatches(batches) {
        this.modifyBatches = batches;
        let dialog = this._getDialog(2);
        dialog.setModifyBatches(batches);
      },
      addNewBatch() {
        if (this.newTimeOnline == null || this.newTimeOffline == null) {
          this.$message({
            type: 'error',
            message: "请选择正确的时间"
          });
          return;
        }
        let online = Math.floor(this.newTimeOnline.getTime() / 1000);
        let offline = Math.floor(this.newTimeOffline.getTime() / 1000);
        if (this.$store.state.debug === false) {
          FrameCtrl.getIns().appendBatch(this.$store.state.robot.dwGroupID, online, offline);
        }
        this.newBatchPage = false;
      },
      setPunIn(idAry) {
        this.$refs.putInDialog.setFilterRobot(idAry);
      }
    },
    components: {
      'my-button': MyButton,
      'nav-footer': NavFooter,
      'toggle-button': ToggleButton,
      'new-built-dialog': NewBatchDialog,
      'modify-dialog': ModifyDialog,
      'put-in-dialog': PutInDialog,
      'accompony': Accompony
    }
  }
</script>

<style   scoped type="text/stylus" lang="stylus">
  @import "./stylus/batchMag.styl";
</style>



