const batchRobotStore = {
  state: {
    batchBatchRobotAry: [],
    batchRobotCurrentIndex: 1
  },
  mutations: {
    setBatchRobotAry(state, payload) {
      payload.forEach((item, index) => {
        item.count = 0;
        item.index = getIndex(index);
        item.isSelect = false;
      });
      state.batchBatchRobotAry = payload;
    },
    pushBatchRobotAry(state, payload) {
      //判断是修改还是新增
      let robotIndex = -1;
      state.batchBatchRobotAry.forEach((robot, index) => {
        if (robot.dwBatchID === payload.dwBatchID && robot.dwUserID === payload.dwUserID) {
          robotIndex = index;
        }
      });
      if (robotIndex !== -1) {
        payload.isSelect = false;
        payload.index = getIndex(robotIndex + 1);
        //修改的各种属性
        Vue.set(state.batchBatchRobotAry[robotIndex], "dwEnterTime", payload.dwEnterTime);
        Vue.set(state.batchBatchRobotAry[robotIndex], "dwLeaveTime", payload.dwLeaveTime);
        Vue.set(state.batchBatchRobotAry[robotIndex], "wPlayKindID", payload.wPlayKindID);
        Vue.set(state.batchBatchRobotAry[robotIndex], "dwSupplyTemplateID", payload.dwSupplyTemplateID);
        Vue.set(state.batchBatchRobotAry[robotIndex], "dwActionTemplateID", payload.dwActionTemplateID);
        Vue.set(state.batchBatchRobotAry[robotIndex], "index", getIndex(robotIndex + 1));
        Vue.set(state.batchBatchRobotAry[robotIndex], "isSelect", false);
      } else {
        state.batchBatchRobotAry.forEach((item, index) => {
          item.isSelect = false;
          item.index = getIndex(index + 1)
        });
        payload.isSelect = false;
        payload.index = getIndex(state.batchBatchRobotAry.length + 1);
        payload.count = 10 + state.batchBatchRobotAry.length;
        state.batchBatchRobotAry.push(payload);
      }

    },
    deleteBatchRobotAry(state, payload) {
      let deleteIndex = -1;
      //并且groupId也必须一致,必须重新编辑序号
      state.batchBatchRobotAry.forEach((batch, index) => {
        if (batch.dwBatchID === payload.dwBatchID && batch.dwUserID === payload.dwUserID) {
          deleteIndex = index;
        }
      });
      if (deleteIndex !== -1) {
        state.batchBatchRobotAry.splice(deleteIndex, 1);
        state.batchBatchRobotAry.forEach((item, index) => {
          item.isSelect = false;
          item.index = getIndex(index + 1);
        })
      } else {
        console.log("未找到批次机器人:", payload);
      }
    },
    changeBatchRobotIndex(state, payload) {
      //做验证，去除不符合规则的页面
      if (payload < 1 || payload > Math.ceil(state.batchBatchRobotAry.length / 8)) {
        console.warn("错误的页面:", payload);
        return
      }
      state.batchRobotCurrentIndex = payload;
    },
    toggleBatchRobot(state, payload) {
      console.warn(payload);
      let index = state.batchBatchRobotAry.indexOf(payload);
      if (index >= 0 && index < state.batchBatchRobotAry.length) {
        state.batchBatchRobotAry[index].isSelect = !state.batchBatchRobotAry[index].isSelect;
      }
    },
    //全选
    allBatchRobot(state, payload) {
      state.batchBatchRobotAry.forEach(acp => {
        acp.isSelect = payload;
      })
    },
    reverseBatchRobot(state, payload) {
      state.batchBatchRobotAry.forEach(acp => {
        acp.isSelect = !acp.isSelect;
      })
    }
  },
  getters: {
    currentBatchRobot(state) {
      return state.batchBatchRobotAry.slice((state.batchRobotCurrentIndex - 1) * 8, (state.batchRobotCurrentIndex) * 8);
    },
    getAllBatchRobotId(state) {
      let back = [];
      state.batchBatchRobotAry.forEach(item => {
        back.push(item.dwUserID);
      });
      return back;
    },
    totalBatchRobotIndex(state) {
      return Math.ceil(state.batchBatchRobotAry.length / 8);
    },
    getBatchRobotByID(state) {
      return id => {
        console.log("查找id", id);
        let back = state.batchBatchRobotAry.find(item =>
          item.dwUserID == id
        );
        return back;
      }
    },
    getSelectedBatchRobot(state) {
      let back = state.batchBatchRobotAry.filter(item => {
        return item.isSelect === true;
      })
      return back;
    }
  }
};

export {batchRobotStore};

