<script src="../../store/Accompany.js"></script>
<template>
  <div class="put-in-wrapper">
    <el-dialog
      :visible.sync="onlyVisible"
      class="batch-wrapper"
      width="70%"
      custom-class="bbbbbbbb-wrapper"
    >
      <span slot="title" class="dialog-title">
        <div class="title">放入陪玩</div>

      </span>
      <div class="content-wrapper" >
        <div class="line"></div>
        <div class="show-list" >
          <table class="server-table" width="100%">
            <tr class="table-row table-title">
              <th class="table-title-item" width="25" style="flex-grow:1">      </th>
              <th class="table-title-item" width="70" style="flex-grow:1">序号</th>
              <th class="table-title-item" width="70" style="flex-grow:2">ID</th>
              <th class="table-title-item" width="70" style="flex-grow:3">昵称</th>
              <th class="table-title-item" width="70" style="flex-grow:2">性别</th>
              <th class="table-title-item" width="70" style="flex-grow:2">玩家类型</th>
              <th class="table-title-item" width="70" style="flex-grow:2">上级ID</th>
              <th class="table-title-item" width="70" style="flex-grow:2">上级昵称</th>
              <th class="table-title-item" width="70" style="flex-grow:2">上级类型</th>
            </tr>
            <tr v-for="(item, index) in getCurrentFilter" class="table-row table-body">
              <td class="table-body-item" width="25"  style="flex-grow:1">
                <!--<input-->
                <!--name="Fruit"-->
                <!--type="checkbox"-->
                <!--class="check-input"-->
                <!--v-model="item.isSelect"-->
                <!--@click="_inputEvent($event,index)"-->
                <!--/>-->
                <toggle-button class="check-input" :select="item.isSelect" :index="index" @toggle="toggleBtn" ></toggle-button>
              </td>
              <td class="table-body-item" width="70"  style="flex-grow:1">{{item.index}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{item.dwUserID}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{item.szNickName}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:3">{{getGenderStr(item.cbGender)}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{item.cbMemberType}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{item.dwParentID}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{item.wParentNickName}}</td>
              <td class="table-body-item" width="70"  style="flex-grow:2">{{item.wParentType}}</td>



            </tr>
          </table>
        </div>
        <div class="btn-container">
          <div class="btn-wrapper">
            <div class="btn-all">
              <!--<input type="checkbox" @click="_cancel" v-model="btnCancel"><label >{{infoTip}}</label>-->
              <toggle-button class="check-input" :select="btnCancel"  @change="_cancel"></toggle-button>
              <span class="txt">全选</span>
              <toggle-button class="check-input" :select="btnReverse"  @change="_reverse"></toggle-button>
              <span class="txt">反选</span>
            </div>

          </div>
        </div>
        <nav-footer class="nav-footer" :totalLength="filterAry.length" :currentIndex="filterIndex" @changeIndex="_onChangePages"></nav-footer>

        <div class="btn-close">
          <my-button :commonSrc="agreeSrc" class="agree-btn" eventName="agree-event" @agree-event="addSelected"></my-button>
        </div>
      </div>
    </el-dialog>
  </div>
</template>

<script>

  import MyButton from '../myButton/MyButton.vue';
  import NavFooter from '../navFooter/NavFooter.vue';
  import ToggleButton from '../myToggleButton/MyToggleButton.vue';
  import {mapGetters} from 'vuex';

  import {FrameCtrl} from '../../frame/FrameCtrl'

  export default {
    data() {
      return {
        onlyVisible: this.dialogVisible,
        addSrc: "btn_add",
        editSrc: "btn_edit",
        deleteSrc: "btn_delete",
        deleteAllSrc: "btn_delete_all",
        agreeSrc: "btn_agree",
        modifyAllSrc: "btn_modify_all",
        filterAry: [],
        filterIndex: 1,
        filterId: [],
        infoTip: "全选",
        btnCancel: false,
        btnReverse: false,
        addApy: false,
        addType: 2,
        batchID: 0
      }
    },
    mounted() {
      console.log("put in:", this.$store.state.add.acyCurrentIndex);
    },
    computed: {
      ...mapGetters(['currentApy', 'totalAddIndex', 'getSelectedApy', 'getFilterApy']),
      getCurrentFilter() {
        return this.filterAry.slice((this.filterIndex - 1) * 8, (this.filterIndex) * 8);
      }
    },
    methods: {
      handleClose(done) {
        done();
      },
      _close() {
        console.log("监听到事件： 关闭");
        this.$emit("closeDialog", 3);
      },
      closeDialog() {
        this.onlyVisible = false;
      },
      openDialog() {
        this.onlyVisible = true;
      },
      editRobot() {},
      deleteRobot() {},
      setFilterRobot(ary) {
        this.filterId = ary;
        this.filterAry = this.getFilterApy(this.filterId);
        console.log("dot...");
      },
      _cancel(index) {
        console.log("选中:", index);
        this.btnCancel = index;
        this.$store.commit('allAcp', index);

      },
      _reverse(index) {
        console.log("选中:", index);
        this.btnReverse = index;
        this.$store.commit('reverseAcp', index);
      },
      _addApy() {
        this.addApy = !this.addApy;
      },
      onSelectType(obj) {
        this.addType = obj.type;
      },
      _onChangePages(index) {
        this.filterIndex = index;
        //this.$store.commit("changeFilterIndex", index);
      },
      toggleBtn(event) {
        let index = event.type;
        let target = this.filterAry[index + (this.filterIndex - 1) * 8];
        this.$store.commit('toggleAcp', target);
      },
      onModifyAll() {
        //要将选中的信息导入的批量修改页面吗？程序需要知道，页面不需要
        this.$emit('open-page', 2);
      },
      getGenderStr(gender) {
        switch (gender) {
          case 1: {
            return "男";
          }
          case 2: {
            return "女";
          }
          default: {
            return "未知";
          }

        }
      },
      setBatchID(id) {
        this.batchID = id;
      },
      addSelected() {
        //如果已经存在于该批次的机器人，是否不显示
        let back = this.getSelectedApy;
        let type = 2;
        for(let n = 0; n < back.length; n++) {
          let data = {
            dwBatchID: this.batchID,
            dwUserID: back[n].dwUserID,
            dwEnterTime: 0,
            dwLeaveTime: 0,
            wPlayKindID: [0, 0, 0],
            dwSupplyTemplateID: 0,
            dwActionTemplateID: [0, 0, 0]
          };
          if (this.$store.state.debug === false ) {
            console.log(this.batchID, type, data);
            FrameCtrl.getIns().modifyBatch(this.batchID, type, data);
          } else {
            this.$store.commit('pushBatchRobotAry', data);
          }
          this.$emit('open-page', 1);
          back[n].isSelect = false;
        }
      }
    },
    props: {
      dialogVisible: {
        type: Boolean,
        default: false
      }
    },
    components: {
      'my-button': MyButton,
      'nav-footer': NavFooter,
      'toggle-button': ToggleButton
    }
  }
</script>

<style  scoped type="text/stylus" lang="stylus">
  @import "./stylus/putIn.styl";
</style>



