let localData = [];
function getIndex(index) {
  switch (true) {
    case index < 10 : {
      return "00" + index;
    }
    case index < 100 : {
      return "0" + index;
    }
    case index < 1000 : {
      return "" + index;
    }
  }
}
const accompanyStore =  {
  state: {
    acyAry: [].concat(localData),
    acyCurrentIndex: 1,
    filterIndex: 1,
    filterLength: 0
  },
  getters: {
    currentApy(state) {
      return state.acyAry.slice((state.acyCurrentIndex - 1) * 8, (state.acyCurrentIndex) * 8);
    },
    getTypePartner(state) {
      let back = [];
      state.acyAry.forEach(item => {
        if (item.cbMemberType == 2) {
          back.push(item);
        }
      })
      //let back = state.acyAry.filter(item => item.cbMemberType === 2);
      return back;
    },
    getFilterApy(state) {
      return idAry => {
        let back = [].concat(state.acyAry);
        idAry.forEach(id => {
          //删除数组中userId相同的对象
          for(let n = 0; n < back.length; n++) {
            if (back[n].dwUserID == id) {
              back.splice(n, 1);
              break;
            }
          }
        });
        state.filterLength = back.length;
        //return back.slice((state.filterIndex - 1) * 8, (state.filterIndex) * 8);
        return back;
      }
    },
    totalAcyIndex(state) {
      return Math.ceil(state.acyAry.length / 8);
    },
    getSelectedApy(state) {
      let back = state.acyAry.filter(item => {
        return item.isSelect === true;
      });
      return back;
    },
    getRobotNameByID(state) {
      return id => {
        let back = state.acyAry.find(item => item.dwUserID === id);
        if (back == null) {
          return "未找到机器人";
        } else {
          return back.szNickName;
        }
      }
    }
  },
  mutations: {
    setAcpAry(state, payload) {
      //需要同时设置上级昵称、上级id和上级类型

      payload.forEach((item, index) => {
        item.isSelect = false;
        item.index = getIndex(index + 1 + state.acyAry.length);
        let parent = payload.find(parent => parent.dwUserID === item.dwParentID);
        if (parent === undefined) {
          console.log("未检测到上级:", item)
        } else {
          item.wParentNickName = parent.szNickName;
          item.wParentType = parent.cbMemberType;
        }
      });
      state.acyAry = state.acyAry.concat(payload);
    },
    pushAcpAry(state, payload) {
      //首先判断是否有
      let index = -1;
      state.acyAry.forEach((item, itemIndex) => {
        if (item.dwUserID === payload.dwUserID) {
          index = itemIndex;
        }
      });
      if (index !== -1) {
        //修改
        payload.isSelect = false;
        payload.index = getIndex(index);
        Vue.set(state.acyAry[index], "cbGender", payload.cbGender);
        Vue.set(state.acyAry[index], "index", getIndex(index));
        Vue.set(state.acyAry[index], "isSelect", false);
        // state.acyAry[index] = payload;
      } else {
        //新增
        console.log("新增机器人:");
        payload.isSelect = false;
        payload.index = getIndex(state.acyAry.length);
        state.acyAry.push(payload);
      }

    },
    deleteAcpAry(state, payload) {
      let deleteIndex = -1;
      //并且groupId也必须一致,必须重新编辑序号
      state.acyAry.forEach((niu, index) => {
        if (niu.dwUserID === payload.dwUserID && niu.dwGroupID === payload.dwGroupID) {
          deleteIndex = index;
        }
      });
      if (deleteIndex !== -1) {
        state.acyAry.splice(deleteIndex, 1);
        state.acyAry.forEach((niu, index) => {
          niu.index = getIndex(index + 1);
        })
      }
    },
    changeAcpIndex(state, payload) {
      state.acyCurrentIndex = payload;
    },
    changeFilterIndex(state, payload) {
      state.filterIndex = payload;
    },
    toggleAcp(state, payload) {
      console.warn(payload);
      let index = state.acyAry.indexOf(payload);
      if (index >=0 && index < state.acyAry.length) {
        state.acyAry[index].isSelect = !state.acyAry[index].isSelect;
      }
    },
    //全选
    allAcp(state, payload) {
      state.acyAry.forEach(acp => {
        acp.isSelect = payload;
      })
    },
    reverseAcp(state, payload) {
      state.acyAry.forEach(acp => {
        acp.isSelect = !acp.isSelect;
      })
    },
    clearAcy(state) {
      state.acyAry = [];
    }
  }
};

export {accompanyStore};



