<template>
    <div class="club-wrapper">
      <div class="search">
        <span class="search-txt">新增俱乐部ID：</span>
        <input type="text" class="club-id" v-model="clubID">
        <span class="search-txt">俱乐部名称：</span>
        <input type="text" class="club-name" v-model="clubName" readonly>
        <div class="btn-config">
          <my-button :commonSrc="configSrc" class="img-btn-config" eventName="onConfig" @onConfig="onConfig"></my-button>
        </div>
      </div>
      <div class="show-list">
        <table class="club-table" width="100%">
          <tr class="table-row table-title">
            <th class="table-title-item" width="70" style="flex-grow:2">序号</th>
            <th class="table-title-item" width="70" style="flex-grow:2">俱乐部ID</th>
            <th class="table-title-item" width="70" style="flex-grow:3">俱乐部昵称</th>
            <th class="table-title-item" width="70" style="flex-grow:2">机器人数量</th>
            <th class="table-title-item" width="70" style="flex-grow:8">操作项</th>
          </tr>
          <tr v-for="(item, index) in showGroup" class="table-row table-body">
            <td class="table-body-item" width="70"  style="flex-grow:2">{{item.index}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{item.dwGroupID}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:3">{{item.szGroupName}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{item.number}}</td>
            <td class="table-body-item table-item-operate" width="70"  style="flex-grow:8">
              <div height="36" class="table-btn-wrapper">
                <my-button class="table-btn"  :commonSrc="editSrc" :targetIndex="index" eventName="editClub" @editClub="editClub"></my-button>
              </div>

            </td>
          </tr>
        </table>
      </div>
      <nav-footer class="nav-footer" :totalLength="$store.state.groupList.length" :currentIndex="$store.state.gourpCurrentIndex" @changeIndex="_onChangePages" @click-once="clearSearch"></nav-footer>

    </div>
</template>

<script>
  import NavFooter from '../navFooter/NavFooter.vue';
  import MyButton from '../myButton/MyButton.vue';
  import ToggleButton from '../myToggleButton/MyToggleButton.vue';
  import {mapGetters} from 'vuex';

  import {FrameCtrl} from '../../frame/FrameCtrl'


  export default {
    data() {
      return {
        clubAry: [],
        clubID: 0,
        clubName: "",
        editSrc: "btn_edit",
        deleteSrc: "btn_delete",
        deleteAllSrc: "btn_delete_all",
        configSrc: "btn_config",
        infoTip: "全选",
        btnCancel: false,
        currentTable: [
          {
          index: '001',
          id: '1688888',
          nickName: '拉风的大蘑菇',
          robotCount: "188",
          isSelect: false
        }, {
          index: '002',
          id: '1688888',
          nickName: '拉风的大蘑菇',
          robotCount: "188",
          isSelect: false
        }, {
          index: '003',
          id: '1688888',
          nickName: '拉风的大蘑菇',
          robotCount: "188",
          isSelect: false
        }, {
          index: '004',
          id: '1688888',
          nickName: '拉风的大蘑菇',
          robotCount: "188",
          isSelect: false
        }, {
          index: '005',
          id: '1688888',
          nickName: '拉风的大蘑菇',
          robotCount: "188",
          isSelect: false
        }, {
          index: '006',
          id: '1688888',
          nickName: '拉风的大蘑菇',
          robotCount: "188",
          isSelect: false
        }, {
          index: '007',
          id: '1688888',
          nickName: '拉风的大蘑菇',
          robotCount: "188",
          isSelect: false
        }, {
          index: '008',
          id: '1688888',
          nickName: '拉风的大蘑菇',
          robotCount: "188",
          isSelect: false
        }],
        searchAry: [],
        search: false
      }
    },
    computed: {
      ...mapGetters(['currentGroup', 'totalGroupIndex']),
      showGroup() {
        return this.search ? this.searchAry : this.currentGroup;
      }
    },
    methods: {
      onConfig() {
        /*待修改*/
        console.log("确认选择");
        //搜索并且将其展示在列表上
        let group = this.$store.getters.getGroupById(parseInt(this.clubID));
        if (group === undefined) {
          this.search = true;
          this.clubName = "未查找到俱乐部";
          this.searchAry = [].concat();
        } else {
          this.search = true;
          this.clubName = group.szGroupName;
          this.searchAry = [].concat(group);
        }

      },
      _inputEvent(event, type) {

      },
      _cancel() {
        this.btnCancel = !this.btnCancel;
        this.currentTable.forEach(item => {
          item.isSelect = this.btnCancel;
        });
      },
      _onChangePages(index) {
        this.$store.commit("changeGroupIndex", index);
      },
      clearSearch() {
        this.search = false;
        this.clubName = "";
      },
      deleteClub(index) {
        console.log("删除俱乐部:", index);
        this.currentTable.splice(index, 1);
      },
      deleteAll() {
        let deleteVec = [];
        this.currentTable.forEach(item => {
          if (item.isSelect == true) {
            deleteVec.push(item);
          }
        });
        deleteVec.forEach(item => {
          let index = this.currentTable.indexOf(item);
          if (index >= 0) {
            this.currentTable.splice(index, 1);
          }
        })

      },
      toggleBtn(event) {
        let index = event.type;
        this.currentTable[index].isSelect = !this.currentTable[index].isSelect;
      },
      editClub(index) {
        //编辑当前列表的俱乐部
        let club = this.showGroup[index];
        let id = this.showGroup[index].dwGroupID;
        console.log(id);

        //根据id查找并且路由
        //此时是加载群组还是加载群组成员
        if (this.$store.state.debug === false) {
          //清空batch以及apy
          this.$store.commit("clearBatches");
          this.$store.commit("clearAcy");
          FrameCtrl.getIns().loadGroupMember(id);
          FrameCtrl.getIns().loadGroupBatch(id);

          this.$store.commit('setClubID', club);
//          this.$router.push({path: '/main/serverControl'})

        } else {
//          this.$router.push({path: '/main/serverControl'})
        }
        this.$emit("changeNav", 1);
        //最好等到等到回调的时候再进行路由
        //设置俱乐部id和名字

      },
      routeToServer() {
        //作为加载群组成员的回调
        this.$router.push({path: '/main/serverControl'})
      }
    },
    components: {
      "nav-footer": NavFooter,
      "my-button": MyButton,
      'toggle-button': ToggleButton
    }
  }
</script>

<style  scoped type="text/stylus" lang="stylus">
  @import "./stylus/clubControl.styl";
</style>



