<template>
    <div class="wrapper">
      <div class="header" ref="header">
        <img src="./resource/login_logo.png" alt="" class="logo">
        <div class="info">
          <div class="user">
            <img src="http://static.galileo.xiaojukeji.com/static/tms/default_header.png" alt="" class="avatar">
            <!--$store.state.userInfo.szNickName-->
            <span class="username">{{$store.state.userInfo ? $store.state.userInfo.szNickName : "未获取到信息"}}</span>
          </div>
          <div class="btn-exit" @click="_exit">
            <i class="icon el-icon-switch-button"></i>
            <span class="text">退出</span>
          </div>
        </div>

      </div>
      <div style="width:100%;" ref="mainWrapper" class="main-wrapper">
          <div class="aside">
            <nav-menu class="menu" :activeIndex="activeIndex" @change="_changeRouter" ref="nav"></nav-menu>
          </div>
          <div class="content-wrapper">
            <keep-alive>
              <router-view class="show-view" @changeNav="changeNav"></router-view>
            </keep-alive>
          </div>
      </div>



    </div>
</template>

<script>
  import NavMenu from '@/components/nav/NavMenu';
  import {mapGetters} from 'vuex';
  import {ServerManager} from "../../ServerManager";
  import {FrameCtrl} from "../../frame/FrameCtrl";
  import {ServiceCtrl} from "../../managers/ServerCtrl";

  const CMessageSuccess = "success";
  const CMessageFail = "error";

  export default {
    data() {
      return {
        activeIndex: 0
      }
    },
    created() {
      FrameCtrl.getIns().setOperaMessageCallBack(this._operaMessage, this);
      ServiceCtrl.getInstance().setStopCallBack(this._stopCallback, this);
    },
    mounted() {
      //pan
      if (this.$store.state.debug === false && this.$store.state.userInfo === null) {
          this.$router.push({path: '/'});
          return;
      }
      this.$nextTick(() => {
        this._adjustHeight();
      })
    },
    methods: {
      _exit() {
//        console.log("退出");
//        this.$router.push({path: '/login'})
        //关闭socket
        ServerManager.getIns().closeServer();
        this.$store.state.userInfo = null;
        this.$router.push({path: '/login'})
        //清空UserInfo
        console.log(this.$route);
      },
      _adjustHeight() {
        let bodyHeight = document.documentElement.clientHeight;
        let headerHeight = this.$refs.header.clientHeight;
        let asideHeight = bodyHeight - headerHeight;
        this.$refs.mainWrapper.style.height = asideHeight + "px";
      },
      _changeRouter(type) {
//        console.log('切换路由', type);
      },
      changeNav(type) {
        console.log("主页面切换:", type);

        this.$refs.nav.changeIndex(type);
      },
      _operaMessage(type, code, msg) {
        let msgType = type ? CMessageSuccess : CMessageFail;

        this.$message({
          message: code + ":" + msg,
          type: msgType
        });
      },
      _stopCallback() {
        this.$message({
          message: "连接断开，请重新登录",
          type: "error"
        });
        this.$router.push({path: '/login'});
      }
    },
    computed: {
      ...mapGetters(['getGroupById'])
    },
    components: {
      "nav-menu": NavMenu,

    },
    props: {
      pwd: {
        type: String
      }
    }
  }
</script>

<style lang="stylus" type="text/stylus" scoped>
  @import "./stylus/main.styl"
</style>



