<template>
    <div class="server-wrapper">
      <div class="header">
        <!--<img src="@@/server_module/title_server.png" class="header-title">-->
        <nav-button :commonSrc="serverSrc" :current="currentServer" @click.native.stop="changeIndex(1)"></nav-button>
        <nav-button :commonSrc="batchSrc" :current="currentBatch" @click.native.stop="changeIndex(2)"></nav-button>
      </div>
      <component :is="currentComponent"></component>
    </div>
</template>

<script>
  //导入两个子组件
  import ServerMag from '../serverManager/ServerManager.vue';
  import BatchMag from '../batchManager/BatchManager.vue';

  import NavButton from './NavButton.vue';

  import {mapGetters} from 'vuex';

  export default {
    data() {
      return {
        componentIndex: 1,
        serverSrc: "nav_server",
        batchSrc: "nav_batch"
      }
    },
    mounted() {
      let group = this.getGroupById(this.$store.state.robot.dwGroupID);
//      if (group === null || group === undefined) {
//        this.$emit("changeNav", 0);
//      }
      console.log(group);
    },
    methods: {
      changeIndex(index) {
        if (this.componentIndex === index) return;
        this.componentIndex = index;
      }
    },
    computed: {
      currentComponent() {
        //根据当前的componentIndex计算出当前的component
        return this.componentIndex == 1 ? ServerMag : BatchMag;
      },
      currentServer() {
        return this.componentIndex === 1;
      },
      currentBatch() {
        return this.componentIndex === 2;
      },
      ...mapGetters(['getGroupById'])
    },
    components: {
      'nav-button': NavButton
    }
  }
</script>

<style   scoped type="text/stylus" lang="stylus">
  @import "./stylus/server.styl";
</style>



