<template>
    <div class="nav-button-wrapper">
      <img :src="currentSrc">
    </div>
</template>

<script>
  export default {
    data() {
      return {
        imgSrc1: '',
        imgSrc0: ''
      }
    },
    mounted() {
      import('@@/server_module/' + this.commonSrc + "_0.png").then(src => {
        this.imgSrc0 = src;
      });
      import('@@/server_module/' + this.commonSrc + "_1.png").then(src => {
        this.imgSrc1 = src;
      });
    },
    computed: {
      currentSrc() {
        return this.current ? this.imgSrc0 : this.imgSrc1;
      }
    },
    props: {
      commonSrc: {
        type: String,
        default: ""
      },
      current: {
        type: Boolean,
        default: false
      }

    }
  }
</script>

<style>
  .nav-button-wrapper {
    cursor: pointer;
  }
</style>



