    FrameCtrl.prototype.getSTemplate = function (szTemplateName, szTemplateRemarks, increaseAry, reduceItem, dwTemplateID) {
        if (dwTemplateID === void 0) { dwTemplateID = 0; }
        console.log(increaseAry, reduceItem);
        var sTemplate = new df.tagAndroidSupplyTemplate();
        //填充参数
        //添加模板默认ID为0；
        sTemplate.dwTemplateID = dwTemplateID;
        sTemplate.szTemplateName = szTemplateName;
        sTemplate.szTemplateRemarks = szTemplateRemarks;
        var buffer = Memory.newLitteEndianByteArray(Len_Tcp_Head);
        sTemplate.onAppend(buffer);
        // buffer.Append_Bytes();
        // return;
        //如何创建一个长度固定的buffer
        //首先Append一个WORD,标识某一项的个数,接着再Append一个加分或者减分子项
        for (var n = 0; n < 3; n++) {
            //包含一个count代表数组长度，还有一个ary
            var item = increaseAry[n];
            var rule = Memory.newLitteEndianByteArray(0);
            for (var n_1 = 0; n_1 < 64; n_1++) {
                rule.Append_Byte(0);
            }
            rule.position(0);
            rule.Append_WORD(item.count);
            for (var m = 0; m < item.count; m++) {
                this.appendIncreaseScoreItem(rule, item.ary[m]);
            }
            buffer.Append_Bytes(rule.getByteArray(), 0, rule.getLength());
        }
        var reduce = Memory.newLitteEndianByteArray(0);
        for (var n = 0; n < 64; n++) {
            reduce.Append_Byte(0);
        }
        reduce.position(0);
        reduce.Append_WORD(reduceItem.count);
        for (var m = 0; m < reduceItem.count; m++) {
            this.appendReduceScoreItem(reduce, reduceItem.ary[m]);
        }
        buffer.Append_Bytes(reduce.getByteArray(), 0, reduce.getLength());
        return buffer;
    };
