const addStore = {
  state: {
    addAry: [
      {
        index: "001",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "002",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "003",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "004",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "005",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "006",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "007",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "008",
        id: "163888",
        nickName: "蛋蛋"
      },
      {
        index: "009",
        id: "163888",
        nickName: "蛋蛋"
      },
    ],
    addCurrentIndex: 1
  },
  mutations: {
    changeAddIndex(state, payload) {
      //做验证，去除不符合规则的页面
      if (payload < 1 || payload > Math.ceil(state.addAry.length / 8)) {
        console.warn("错误的页面:", payload);
        return
      }
      state.addCurrentIndex = payload;
    },
    toggleAdd(state, payload) {
      console.warn(payload);
      let index = state.addAry.indexOf(payload);
      if (index >=0 && index < state.addAry.length) {
        state.addAry[index].isSelect = !state.addAry[index].isSelect;
      }
    },
    //全选
    allAdd(state, payload) {
      state.addAry.forEach(acp => {
        acp.isSelect = payload;
      })
    },
    reverseAdd(state, payload) {
      state.addAry.forEach(acp => {
        acp.isSelect = !acp.isSelect;
      })
    }
  },
  getters: {
    currentAdd(state) {
      return state.addAry.slice((state.addCurrentIndex - 1) * 8, (state.addCurrentIndex) * 8);
    },
    totalAddIndex(state) {
      return Math.ceil(state.addAry.length / 8);
    }
  }
};

export {addStore};






