/**
 * 封装websocket
 */
import { ByteArray } from './ByteArray';
var TcpSocket = /** @class */ (function () {
    function TcpSocket() {
        this.m_eSocketStatus = 0 /* soc_unConnect */;
    }
    TcpSocket.getIns = function () {
        if (TcpSocket.m_Instance == null) {
            TcpSocket.m_Instance = new TcpSocket();
            TcpSocket.m_Instance.init();
        }
        return TcpSocket.m_Instance;
    };
    TcpSocket.prototype.init = function () {
    };
    TcpSocket.prototype.connect = function (url, port) {
        var test = "ws://" + url + ":" + port;
        // let mini = "wss://minigame.foxuc.com:9221";
        console.log(test);
        this._socket = new WebSocket(test);
        this._socket.binaryType = "arraybuffer";
        this.initSocket(this._socket);
        this._ifClose = false;
    };
    //主动断开连接;
    TcpSocket.prototype.close = function () {
        this._ifClose = true;
        if (this._socket) {
            this._socket.close();
        }
    };
    TcpSocket.prototype.initSocket = function (socket) {
        var _this = this;
        socket.onopen = function (ev) {
            _this.onSocketConnect();
        };
        socket.onerror = function (ev) {
            _this.onSocketError(ev);
        };
        socket.onmessage = function (ev) {
            _this.onReceiveMessage(ev);
        };
        socket.onclose = function (ev) {
            _this.onSocketClose(ev);
        };
    };
    TcpSocket.prototype.onReceiveMessage = function (ev) {
        console.log("消息接收", ev);
        //加入缓冲队列
        if (this.m_SocketServiceModule) {
            //获取到的data
            var data = ev.data;
            // let socket = <egret.WebSocket>(ev.target);
            //读取数据流
            var buffer = new ByteArray(data);
            // buffer.setByteArray(data);
            // socket.readBytes(buffer.getByteArray(), 0);
            console.log("\u63A5\u6536\u957F\u5EA6=======" + buffer.getLength());
            this.m_SocketServiceModule.pushRecvBuffer(buffer);
        }
    };
    TcpSocket.prototype.onSocketError = function (e) {
        // let obj = JSON.stringify(e);
        // utils.GameConst.colorConsole("连接错误:");
        console.log(e);
        if (e.data) {
            var obj = JSON.stringify(e.data);
            console.log(JSON.parse(obj));
        }
        // //失败通知
        if (this.m_SocketServiceModule) {
            if (this.isConnected()) {
                //需断线重连
                console.log("断线1");
                this.m_SocketServiceModule.onReconnect();
            }
            else {
                //连接异常 连接错误 超时
                console.log("断线2");
                this.m_SocketServiceModule.onReconnect();
            }
        }
        // //设置socket状态
        this.setConnectStatus(3 /* soc_error */);
        console.log("socket error");
    };
    TcpSocket.prototype.onSocketClose = function (e) {
        // utils.GameConst.colorConsole("TcpSocket连接关闭:");
        console.log(e);
        //服务关闭
        // managers.ServiceCtrl.getInstance().closeService();
        //设置socket状态
        this.setConnectStatus(0 /* soc_unConnect */);
        console.log("socket close");
        this.m_SocketServiceModule.stopConnect();
        //发布前注释
        // managers.FrameManager.getInstance().dismissPopWait();
    };
    TcpSocket.prototype.setConnectStatus = function (status) {
        this.m_eSocketStatus = status;
    };
    //egret.ByteArray
    TcpSocket.prototype.writeBytes = function (bytes, offset, length) {
        // utils.GameConst.colorConsole("TcpSocket写操作");
        var write = bytes.buffer;
        if (!offset) {
            offset = 0;
        }
        if (!length) {
            length = write.byteLength;
        }
        var last = write.slice(offset, offset + length);
        //主动调用关闭以后停止发送数据
        if (!this._ifClose) {
            this._socket.send(last);
        }
    };
    //egret.ByteArray
    TcpSocket.prototype.readBytes = function (bytes, offset, length) {
        // utils.GameConst.colorConsole("TcpSocket读操作");
    };
    /**
     * 连接状态
     */
    TcpSocket.prototype.isConnected = function () {
        // console.log("连接状态是否:", this.m_eSocketStatus == df.eSocketStatus.soc_connected);
        return this.m_eSocketStatus == 2 /* soc_connected */;
    };
    /**
     * TCP服务实例
     */
    TcpSocket.prototype.setServiceModule = function (serviceModule) {
        if (this.m_SocketServiceModule)
            this.m_SocketServiceModule = null;
        this.m_SocketServiceModule = serviceModule;
    };
    /**
     * 连接监听
     */
    TcpSocket.prototype.onSocketConnect = function () {
        // utils.GameConst.colorConsole("TcpSocket连接成功:");
        if (this.m_SocketServiceModule) {
            this.m_SocketServiceModule.socketConnectSuccess();
        }
    };
    return TcpSocket;
}());
export { TcpSocket };
