<template>
    <div class="wrapper">
      <div class="header">
        <img src="./resource/login_logo.png" >
      </div>
      <div class="box">
        <el-input placeholder="账户" v-model="username" clearable class="name-input"></el-input>
        <div class="pwd-div">
          <el-input placeholder="密码" v-model="password" show-password class="pwd-input"></el-input>
          <!--忘记密码按钮-->
          <el-button type="text" class="btn-forget">忘记密码</el-button>
        </div>
        <!--<span class="forget">忘记密码</span>-->
        <div class="checkcha-div">
          <input type="text" class="check-input" placeholder="请输入验证码" v-model="inputCheckcha">
          <canvas class="checkcha" width="115" height="50" id="auth-code" @click="_changeCheck">{{checkcha}}</canvas>
        </div>
        <el-button type="success" class="btn-login" @click="_login">登录</el-button>

      </div>
      <div class="footer">
        <span>Copyright © Foxuc Anthony</span>
        <br/>
        <span>Powered by Vue.js</span>
      </div>
    </div>
</template>

<script>

  import writeAuthCode from '../../utils/checkCha/checkCha.js';
  import {LogonFrame} from '../../frame/LogonFrame';
  import {EventDispatcher} from '../../event/EventDispatcher';
  import {MyEvent} from '../../event/Event';
  import {FrameCtrl} from '../../frame/FrameCtrl';
  import {MD5} from '../../utils/web/MD5';
  import {Message} from '../../utils/web/Message';
  import * as df from '../../utils/define/Define'

  import {ServerManager} from '../../ServerManager';

  import {ServiceCtrl} from "../../managers/ServerCtrl";

  //如何实现通信模块
  //通信模块的对外接口
  //通信存在的时候则进入main页面
  //不存在的时候就login页面并且报错
  //logonFrame如何调动组件的（×）
  //logonFrame必须和组件结合在一起onGameMessage和logonFrame如何结合在一起，自定义事件的形式通知？
  //每个组件里面new一个Frame，frame暴露接口来和Vue组件通信
  export default {
    data() {
      return {
        username: '',
        password: '',
        checkcha: "",
        inputCheckcha: '',
        logonFrame: null
      }
    },
    created() {

    },
    mounted() {
      //等待dom更新以后再调用
      this.$nextTick(() => {
        this._getCheckcha();
      })
    },
    beforeDestroy() {
      console.warn("logon组件被摧毁");
    },
    methods: {
      _login() {
        if (this.inputCheckcha.toString() !== this.checkcha) {
          this.$alert('验证码输入错误', '输入错误', {
            confirmButtonText: '确定',
          }).catch(err => {
            console.log(err);
          });
          this.inputCheckcha = '';
          console.log("弹窗显示验证码错误!");
          return ;
        }
        let password = MD5.MD5_HEX("123456");
        if (this.$store.state.debug === true) {
          this.$router.push({path: 'main/mainControl'});
        } else {
          ServerManager.getIns().runServer();
          ServiceCtrl.getInstance().setStopCallBack(this._stopCallback, this);
          FrameCtrl.getIns().logon(this, {
            username: "AIManager",
            pwd: password
          });
        }
      },
      _register() {

      },
      _forgetPwd() {

      },
      _getCheckcha() {
        this.checkcha = this._getRandomCheck();
        let options = {
          canvasId: "auth-code",/**canvas的id*/
          txt: this.checkcha,/**传入验证码内容*/
          height: 50,/**验证码高度 */
          width: 115/**验证码宽度 */
        };
        writeAuthCode(options);
      },
      _getRandomCheck() {
        return Math.random().toString().substring(2,6);
      },
      _changeCheck() {
        this._getCheckcha();
      },
      onMessage(event) {
        console.log("登录页面:", event);
        let msg = event.data;
        const wSubCmd = msg.wSubCmd;
        switch (wSubCmd) {
          case df.SUB_AS_S_LOGON_SUCCESS: {
            //登录成功
            let success = new df.CMD_AS_S_LogonSuccess();
            success.onInit(msg.cbBuffer);
            this.$message({
              type: "success",
              message: '登录成功:' + success.szNickName
            });
            this.$store.commit('userInfo', success);
            this.$router.push({path: 'main/mainControl'});
            break;
          }
          case df.SUB_AS_S_LOGON_FAILURE: {
            //登录失败
            let failure = new df.CMD_AS_S_LogonFailure();
            failure.onInit(msg.cbBuffer);
            this.$alert('登录失败' + failure.lErrorCode + ':', failure.szErrorDescribe, {
              confirmButtonText: '确定',
            }).catch(err => {
              console.log(err);
            });
            //选择性清空用户名以及密码
            break;
          }
          case df.SUB_AS_S_LOGON_FINISH: {
            //登录完成
            console.warn("登录完成");
            break;
          }
        }
      },
      _disabledBtn() {

      },
      _stopCallback() {
        this.$message({
          message: "连接失败",
          type: "error"
        });
        this.$router.push({path: '/login'});
      }

    }
  }
</script>
<style lang="stylus" type="text/stylus" scoped>
  @import './stylus/login.styl';
</style>



