<template>
  <div class="module-wrapper">
    <div class="header">
      <img src="@@/btn/title_module.png" class="header-title">
    </div>
    <div class="top">
      <div class="left">
        <div class="title">
          <span>对应游戏:</span>
        </div>
        <select v-model="gameType">
          <option :value="112">牛牛</option>
          <option :value="79">三公</option>
        </select>
      </div>
      <div class="right">
        <my-button :commonSrc="moduleSrc" eventName="action-event" @action-event="actionEvent"></my-button>
      </div>
    </div>
    <div class="show-list">
      <table class="club-table" width="100%">
        <tr class="table-row table-title">
          <th class="table-title-item" width="25" style="flex-grow:1">模板ID</th>
          <th class="table-title-item" width="70" style="flex-grow:2">模板名称</th>
          <th class="table-title-item" width="70" style="flex-grow:10">备注</th>
          <th class="table-title-item" width="70" style="flex-grow:3">操作项</th>
        </tr>
        <tr v-for="(item, index) in currentModule" class="table-row table-body">
          <td class="table-body-item" width="25" style="flex-grow:1">{{item.dwTemplateID}}</td>
          <td class="table-body-item" width="70" style="flex-grow:2">{{item.szTemplateName}}</td>
          <td class="table-body-item" width="70" style="flex-grow:10">{{item.szTemplateRemarks}}</td>
          <td class="table-body-item table-item-operate" width="70" style="flex-grow:3">
            <div height="36" class="table-btn-wrapper">
              <my-button class="table-btn" :commonSrc="editSrc" :targetIndex="index" eventName="editModule"
                         @editModule="editModule"></my-button>
              <my-button class="table-btn" height="36" :commonSrc="deleteSrc" :targetIndex="index"
                         eventName="deleteModule" @deleteModule="deleteModule"></my-button>
            </div>

          </td>
        </tr>
      </table>
    </div>
    <nav-footer class="nav-footer" :totalLength="totalLength" @changeIndex="_onChangePages"
                :currentIndex="currentIndex"></nav-footer>
    <action-dialog :actionDialog="actionDialog" @closeActionDialog="closeActionDialog" ref="ActionDialog"
                   :gameType="gameType"></action-dialog>

  </div>

</template>

<script>
  import MyButton from "../myButton/MyButton.vue";
  import NavFooter from '../navFooter/NavFooter.vue';
  import ActionDialog from '../actionModule/ActionModule.vue';

  //根据选择的模板不同来切换数据
  import {FrameCtrl} from '../../frame/FrameCtrl';

  export default {
    data() {
      return {
        moduleSrc: 'btn_add_module',
        editSrc: "btn_edit",
        deleteSrc: "btn_delete",
        deleteAllSrc: "btn_delete_all",
        currentTable: [{
          isSelect: false
        }],
        actionDialog: false,
        gameType: 112
      }
    },
    created() {
    },
    /**
     * 新增模板按钮
     * */
    methods: {
      actionEvent() {
        //1、只需要打开模板即可(如何确定是新增还是修改, 设置)
        this.$refs.ActionDialog.setCommitType(1, null);
        this.$refs.ActionDialog.openDialog();
//        //新增模块
//        let testData = {
//          wGameKindID: 112,
//          szTemplateName: "测试牛牛模板1",
//          szTemplateRemarks: "测试牛牛模板备注",
//          score: null,
//          mode: []
//        };
//        let score = {
//          lScoreArea1: [0, 50],
//          cbModeRate1: [60, 25, 10, 5],
//          lScoreArea2: [100, 199],
//          cbModeRate2: [60, 25, 10, 5],
//          lScoreArea3: [200, 299],
//          cbModeRate3: [60, 25, 10, 5],
//          lScoreArea4: [300, Number.MAX_VALUE],
//          cbModeRate4: [60, 25, 10, 5]
//        };
//        testData.score = score;
//        let mode = [];
//        for (let n = 0; n < 4; n++) {
//          let item = {
//            bRobBanker: false,
//            cbRBCardType: 0,
//            cbRBMultiple: 1,
//            bPushAnte: false,
//            bPACardType: false,
//            cbBetMultiple: 2,
//          };
//          mode.push(item);
//        }
//        testData.mode = mode;
//        if (this.$store.state.debug === false) {
//          FrameCtrl.getIns().appendATemplate(testData.wGameKindID, testData.szTemplateName, testData.szTemplateRemarks, testData.score, testData.mode)
//        } else {
//
//        }
      },
      closeActionDialog() {
        this.$refs.ActionDialog.closeDialog();
      },
      deleteModule(index) {
        this.$confirm("确认删除该模块?").then(() => {
          let deleteId = this.currentModule[index].dwTemplateID;
          let type = this.currentModule[index].wGameKindID;
          if (deleteId === null) return;
          if (this.$store.state.debug === true) {
            console.log('成功删除', deleteId);
            switch (type) {
              case 1: {
                this.$store.commit('deleteNiuModule', deleteId);
                break;
              }
              case 2: {
                this.$store.commit('deleteSanModule', deleteId);
                break;
              }
            }
          } else {
            FrameCtrl.getIns().deleteTemplateA(deleteId, type);
          }
        }).catch(err => {
          console.log('取消删除:', err);
        })
      },
      editModule(index) {
        let module = this.currentModule[index];
        console.log("设置模板：", module);
        this.$refs.ActionDialog.setCommitType(2, module);
        this.$refs.ActionDialog.openDialog();
      },
      _onChangePages(index) {
        if (this.gameType === 112) {
          this.$store.commit("changeNiuIndex", index);
        } else if (this.gameType === 79) {
          this.$store.commit("changeSanIndex", index);
        }

      },

    },
    computed: {
      currentModule() {
        return this.gameType === 112 ? this.$store.getters.currentNiu : this.$store.getters.currentSan;
      },
      totalLength() {

        return this.gameType === 112 ? this.$store.state.niu.niuAry.length : this.$store.state.san.sanAry.length;
      },
      currentIndex() {
        return this.gameType === 112 ? this.$store.state.niu.niuCurrentIndex : this.$store.state.san.sanCurrentIndex;
      }
    },
    components: {
      'my-button': MyButton,
      'nav-footer': NavFooter,
      'action-dialog': ActionDialog
    }
  }
</script>

<style scoped type="text/stylus" lang="stylus">
  @import "./stylus/moduleControl.styl";
</style>



