function getObj(obj) {
  let copy;
  //copy = {...obj};
  copy = JSON.parse(JSON.stringify(obj));
  return copy;
}
const gameKind = {
  state: {
    kindAry: [
      {
        wKindID: 112,
        szKindName: "牛牛"
      },
      {
        wKindID: 79,
        szKindName: "三公"
      }
    ],
    niuCurrentIndex: 1
  },
  mutations: {
    setKindAry(state, payload) {
      state.kindAry = payload;
    }
  },
  getters: {
    getKindName(state) {
      return (id) => {
        let game = state.kindAry.find(item => item.wKindID == id);
        if (game != null) {
          return game.szKindName;
        } else {
          return "无";
        }

      }
    }
  }
}

export {gameKind};



