<template>
    <div class="my-button-wrapper"
         @mouseup.prevent="onButtonUp"
         @mousedown.prevent="onButtonDown"
         @mouseout.prevent="onButtonUp"
         @click.prevent="onClick"
         ref="buttonWrapper"
    >
      <img :src="currentSrc" ref="currentImg"  :height="getHeight" :width="getWidth">
    </div>
</template>

<script>
//  import src0 from ''

  export default {
    data() {
      return {
        imgSrc0: "",
        imgSrc1: "",
        currentSrc: ""
      }
    },
    mounted() {
      import('@@/btn/' + this.commonSrc + "_0.png").then(src => {
        this.imgSrc0 = src;
        this.currentSrc = src;
      });
      import('@@/btn/' + this.commonSrc + "_1.png").then(src => {
        this.imgSrc1 = src;
      });
      this.$nextTick(() => {
        this._adjust()
      })

    },
    methods: {
      onButtonDown() {
        this.currentSrc = this.imgSrc1;
      },
      onButtonUp() {
        this.currentSrc = this.imgSrc0;
      },
      onClick() {
        if (this.eventName.length === 0) return;
        this.$emit(this.eventName, this.targetIndex);
      },
      _adjust() {
        let height = parseInt(this.myHeight);
        let width = parseInt(this.myWidth);
        if (isNaN(height) || isNaN(width)) return;
//        console.log(width, height);
//        if (height !== 0 && width !== 0) {
//          console.log("进行适配", width, height);
//          this.$refs.currentImg.style.width = width + 'px';
//          this.$refs.currentImg.style.height = height + 'px';
//          console.log(this.$refs.currentImg.style.width);
//          console.log(this.$refs.currentImg.style.height);
//        }
      }
    },
    computed: {
      getWidth() {
        let width = parseInt(this.myWidth);
        if (isNaN(width) || width === 0) return;
//        console.log("width", width);
        if (width !== 0) return width + 'px';
      },
      getHeight() {
//        console.log(this.myHeight);
        let height = parseInt(this.myHeight);
        if (isNaN(height) || height === 0) return;
//        console.log("height", height);
        if (height !== 0) return height + 'px';
      }
    },
    props: {
      commonSrc: {
        type: String,
        default: ""
      },
      eventName: {
        type: String,
        default: ""
      },
      targetIndex: {
        type: Number,
        default: 0
      },
      myWidth: {
        type: Number,
        default: 0
      },
      myHeight: {
        type: Number,
        default: 0
      }
    }
  }
</script>

<style>
  .my-button-wrapper{
    cursor: pointer;
  }
</style>



