        editSrc: "btn_edit",
        deleteSrc: "btn_delete",
        deleteAllSrc: "btn_delete_all",
        currentTable: [{
          isSelect: false
        }],
        ruleDialog: false
      }
    },
    methods: {
      actionEvent() {
        this.$refs.RuleDialog.setCommitType(1, null);
        this.$refs.RuleDialog.openDialog();
        //新增模板
