      },
      closeRuleDialog() {
        this.$refs.RuleDialog.closeDialog();
      },
      deleteModule(index) {
        console.log("准备删除模板:", index);
        this.$confirm("确认删除该模块?").then(() => {
          let deleteId = this.currentRule[index].dwTemplateID;
          if (deleteId === null) return;
          if (this.$store.state.debug === true) {
            console.log('成功删除', deleteId);
            this.$store.commit('deleteRuleModule', deleteId)
          } else {
            FrameCtrl.getIns().deleteTemplateS(deleteId);
          }
        }).catch(err => {
          console.log('取消删除:', err);
        })
      },
      editModule(index) {
        //获得当前模板的信息
        let rule = this.currentRule[index];
        console.log(rule);
        this.$refs.RuleDialog.setCommitType(2, rule);
        this.$refs.RuleDialog.openDialog();
      },
      _onChangePages(index) {
        this.$store.commit("changeRuleIndex", index);
      },
