<template>
    <div class="nav-footer-wrapper">
      <div class="total">共 <span style="color:#f7902b">{{totalPages}}</span> 页</div>
      <div class="right-wrapper">
        <div class="btn-first btn"
             @mouseup.prevent="onButtonUp($event)"
             @mousedown.prevent="onButtonDown($event)"
             @mouseout.prevent="onButtonOut($event)"
             @click.stop="onBtn('home')"
        >
          <!--<el-card shadow="hover">-->
            首页
          <!--</el-card>-->
        </div>
        <div class="btn-pre btn" @mouseup.prevent="onButtonUp($event)" @mousedown.prevent="onButtonDown($event)" @mouseout.prevent="onButtonOut($event)" @click.stop="onBtn('pre')">上一页</div>
        <div class="current-page btn">{{currentIndex}}</div>
        <div class="btn-next btn btn" @mouseup.prevent="onButtonUp($event)" @mousedown.prevent="onButtonDown($event)" @mouseout.prevent="onButtonOut($event)" @click.stop="onBtn('next')">下一页</div>
        <div class="btn-last btn" @mouseup.prevent="onButtonUp($event)" @mousedown.prevent="onButtonDown($event)" @mouseout.prevent="onButtonOut($event)" @click.stop="onBtn('last')">尾页</div>
        <input type="text" class="goto" v-model="goTo">
        <div class="btn-go btn" @mouseup.prevent="onButtonUp($event)" @mousedown.prevent="onButtonDown($event)" @mouseout.prevent="onButtonOut($event)" @click.stop="onBtn('go')">GO</div>
      </div>

    </div>
</template>

<script>
  export default {
    data() {
      return {
        goTo: 1,
        currentBtn: 1
      }
    },
    computed: {
      totalPages() {
        return Math.ceil(this.totalLength / 8);
      }
    },
    props: {
      totalLength: {
        type: Number,
        default: 0
      },
      currentIndex: {
        type: Number,
        default: 0
      }
    },
    methods: {
      onButtonUp(event) {
//        console.log(event);
      },
      onButtonDown(event){
        console.log("down");
      },
      onButtonOut(event) {

      },
      onBtn(name) {
        this.$emit('click-once');
        let index = -1;
        switch (name) {
          case 'home': {
            if (this.currentIndex === 1) return;
            index = 1;
            break;
          }
          case 'pre': {
            if (this.currentIndex === 1) return;
            index = this.currentIndex - 1;
            break;
          }
          case 'next': {
            if (this.currentIndex >= this.totalPages) return;
            index = this.currentIndex + 1;
            break;
          }
          case 'last': {
            if (this.currentIndex >= this.totalPages) return;
            index = this.totalPages;
            break;
          }
          case 'go': {
            let go = parseInt(this.goTo);
            if (go === this.currentIndex || go < 1 || go > this.totalPages) return;
            index = go;
            break;
          }
        }
        this.$emit('changeIndex', index);
      }
    }
  }
</script>

<style  scoped type="text/stylus" lang="stylus">
  .nav-footer-wrapper
    display: flex;
    justify-content space-between
    background #f9fbfd;
    width: 98%;
    height: 40px;
    border 1px solid #e2e6ec;
    padding-right 15px;
    box-sizing border-box;
    .total
      height 100%;
      line-height 40px;
      padding-left: 10px;
    .right-wrapper
      display flex;
      .active
        background: #fff;
        color: bisque;
      .btn
        height 100%;
        line-height 40px;
        cursor: pointer;
        border-left: 1px solid #e2e6ec;
        padding 0 10px;
      .current-page
        text-align center;
        width: 45px;
        background: #fc9409
        color: #fff
      .goto
        margin auto 0;
        width 30px;
        height 20px;
        text-align center
      .btn-go
        border-left: 0
        color: #178eb9
</style>



