<template>
    <div class="server-manager-wrapper">
      <div class="header">
      <div class="txt">
        <div class="club-wrapper">
          <span class="left">俱乐部ID：</span>{{$store.state.robot.dwGroupID}}
        </div>
        <div class="club-wrapper">
          <span class="left">俱乐部名称：</span>{{getGroupName($store.state.robot.dwGroupID)}}
        </div>


      </div>

      <div class="buttons">
        <my-button class="btn-build main-button" :commonSrc="addSrc" event-name="addApy" @addApy="_addApy"></my-button>
      </div>
      </div>
      <div class="show-list">
        <table class="server-table" width="100%">
          <tr class="table-row table-title">
            <th class="table-title-item" width="25" style="flex-grow:1">      </th>
            <th class="table-title-item" width="70" style="flex-grow:1">序号</th>
            <th class="table-title-item" width="70" style="flex-grow:2">ID</th>
            <th class="table-title-item" width="70" style="flex-grow:3">昵称</th>
            <th class="table-title-item" width="70" style="flex-grow:2">性别</th>
            <th class="table-title-item" width="70" style="flex-grow:2">玩家类型</th>
            <th class="table-title-item" width="70" style="flex-grow:2">上级ID</th>
            <th class="table-title-item" width="70" style="flex-grow:2">上级昵称</th>
            <th class="table-title-item" width="70" style="flex-grow:2">上级类型</th>
            <th class="table-title-item" width="70" style="flex-grow:8"></th>
          </tr>
          <tr v-for="(item, index) in currentApy" class="table-row table-body">
            <td class="table-body-item" width="25"  style="flex-grow:1">
              <toggle-button class="check-input" :select="item.isSelect" :index="index" @toggle="toggleBtn"></toggle-button>
            </td>
            <td class="table-body-item" width="70"  style="flex-grow:1">{{item.index}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{item.dwUserID}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:3">{{item.szNickName}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{getGenderStr(item.cbGender)}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{getTypeName(item.cbMemberType)}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{item.dwParentID}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{item.wParentNickName}}</td>
            <td class="table-body-item" width="70"  style="flex-grow:2">{{item.wParentType}}</td>

            <td class="table-body-item table-item-operate" width="70"  style="flex-grow:8">
              <div height="36" class="table-btn-wrapper">
                <my-button class="table-btn"  :commonSrc="editSrc" :targetIndex="index" eventName="editRobot" @editRobot="editRobot"></my-button>
                <my-button class="table-btn" height="36" :commonSrc="deleteSrc" :targetIndex="index" eventName="deleteRobot" @deleteRobot="deleteRobot"></my-button>
              </div>

            </td>
          </tr>
        </table>
      </div>

      <div class="btn-container">
        <div class="btn-wrapper">
          <div class="btn-all">
            <toggle-button class="check-input" :select="btnCancel"  @change="_cancel"></toggle-button>
            <span class="txt">{{infoTip}}</span>
          </div>
          <div class="sub-wrapper">
            <!--<my-button :commonSrc="modifyAllSrc" class="sub-button" eventName="modify-event" @modify-event="modifyEvent"></my-button>-->
            <my-button :commonSrc="deleteAllSrc" class="sub-button" @click.native.stop="deleteSelectedRobots"></my-button>
          </div>

        </div>
      </div>
      <nav-footer class="nav-footer" :totalLength="$store.state.accompany.acyAry.length" :currentIndex="$store.state.accompany.acyCurrentIndex" @changeIndex="_onChangePages"></nav-footer>

      <el-dialog
        title="添加陪玩"
        :visible.sync="addApy"
        width="30%"
        class="add-wrapper"
        >
        <div class="content-wrapper">
          <div class="line"></div>
          <div class="add-num">
            <span class="txt">添加数量:</span>
            <input type="number" class="addInput" v-model="addCount" @input="clearNoNum">
          </div>
          <div class="add-type">
            <span class="txt">添加类型:</span>
            <div class="button">
              <toggle-button :select="addType == 0" :index="0" @toggle="onSelectType"></toggle-button><span>会员</span>
              <toggle-button :select="addType == 2" :index="2" @toggle="onSelectType" class="right-button"></toggle-button><span>合伙人</span>
            </div>
          </div>
          <div class="add-num" v-show="addType == 2">
            <span class="txt">上级信息:</span>
            <select v-model="dwParentGameID">
              <option :value="0" disabled selected hidden class="tip">昵称/ID</option>
              <option v-for="(item, index) in $store.getters.getTypePartner" :value="item.dwUserID">{{item.dwUserID}}/{{item.szNickName}}</option>

            </select>
          </div>
          <div class="btn-close">
            <my-button :commonSrc="agreeSrc" class="agree-btn" eventName="agree-event" @agree-event="addConfig"></my-button>
          </div>
        </div>


      </el-dialog>
      <accompony :dialogVisible="accomponyDialog" @closeDialog="closeDialog" ref="accomponyDialog"></accompony>

      <div class="father-wrapper">
        <!--编辑性别界面-->
        <el-dialog
          title="基本信息"
          :visible.sync="baseInfo"
          width="30%"
          class="info-wrapper"
          top="30vh"
        >
          <div class="line"></div>
          <div class="content-wrapper">
            <div class="base-info">
              <div class="avatar">avatar</div>
              <div class="info">
                <div class="number">
                  <span class="txt">序号: </span><span class="index">{{getCurrentRobotIndex}}</span>
                  <span class="txt">ID: </span><span class="id">{{getCurrentRobotID}}</span>
                </div>
                <div class="nick-name">
                  <span class="txt">昵称:</span>
                  <input type="text" class="name" readonly="readonly" v-model="name"/>
                </div>
                <div class="other">
                  <div class="sex">
                    <span class="txt">性别:</span>
                    <div class="sex-radio">
                      <label><input type="radio" name="sex" :value="1" v-model="currentRobotGender"/> 男</label>
                      <label><input type="radio" name="sex" :value="2" v-model="currentRobotGender"/> 女</label>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="btn-close">
              <my-button :commonSrc="agreeSrc" class="agree-btn" eventName="agree-event" @agree-event="_configRobotModify"></my-button>
            </div>
          </div>


        </el-dialog>

      </div>
    </div>
</template>

<script>
  import MyButton from '../myButton/MyButton.vue';
  import NavFooter from '../navFooter/NavFooter.vue';
  import ToggleButton from '../myToggleButton/MyToggleButton.vue';
  import {mapGetters} from 'vuex';

  //陪玩页面
  import Accompony from '../accompony/Accompony.vue';

  import {FrameCtrl} from '../../frame/FrameCtrl';


  export default {
    data() {
      return {
        addSrc: "btn_add",
        editSrc: "btn_edit",
        deleteSrc: "btn_delete",
        deleteAllSrc: "btn_delete_all",
        agreeSrc: "btn_agree",
        infoTip: "全选",
        btnCancel: false,
        addApy: false,
        baseInfo: false,
        addCount: 1,
        addType: 0,
        dwParentGameID: 0,
        accomponyDialog: false,
        name: "爱的魔力转圈圈",
        //当前正在编辑机器人的数据
        currentEditRobot: null,
        currentRobotGender: 1
      }
    },
    created() {

    },
    mounted() {
      //如果没有store，nav - 0

    },
    computed: {
      ...mapGetters(['currentApy', 'totalGroupIndex', 'getGroupById', 'getTypePartner']),
      getCurrentRobotIndex() {
        if (this.currentEditRobot) {
          return this.currentEditRobot.index;
        } else {
          return "未获取到index"
        }
      },
      getCurrentRobotID() {
        if (this.currentEditRobot) {
          return this.currentEditRobot.dwUserID;
        } else {
          return "未获取到ID"
        }
      }
    },
    methods: {
      getTypeName(kind) {
        switch (kind) {
          case 0: {
            return "会员";
          }
          case 2: {
            return "合伙人";
          }
        }
      },
      getGroupName(id) {
        if (id === undefined) return "";
        if (this.getGroupById(id)) {
          return this.getGroupById(id).szGroupName;
        } else {
          return "未获取到俱乐部名称";
        }
      },
      toggleBtn() {},
      editRobot(index) {
        //打开编辑页面
        console.log("编辑页面:", index);
        let robot = this.currentApy[index];
        console.log(robot);
        this.currentEditRobot = robot;
        this.name = robot.szNickName;
        this.currentRobotGender = robot.cbGender === 0 ? 1 : robot.cbGender;
        this.baseInfo = true;
      },
      /**
       * 确定机器人修改
       * */
      _configRobotModify() {
        let robot= this.currentEditRobot;
        let [groupID, userID, gender] = [robot.dwGroupID, robot.dwUserID, this.currentRobotGender];
        console.log(robot, gender);
        if (gender === robot.cbGender) return;
        if (this.$store.state.debug === false ) {
          console.log(robot);
          console.log(robot.dwGroupID, robot.dwUserID, 1);
          FrameCtrl.getIns().modifyAndroid(groupID, userID, gender);
        } else {
          //this.
          let newRobot = {};
          for(let key in robot) {
            newRobot[key] = robot[key];
          }
          newRobot.cbGender = gender;
          this.$store.commit('pushAcpAry', newRobot);
        }
        this.baseInfo = false;
      },
      /**
       * 删除单个机器人
       * @param index: 当前的数组的index
       * */
      deleteRobot(index) {
        this.$confirm("确定删除该机器人?").then(() => {
          let deleteId = this.currentApy[index].dwUserID;
          let groupId = this.currentApy[index].dwGroupID;
          if (deleteId === null) return;
          if (this.$store.state.debug === true) {
            console.log('成功删除', deleteId);
            this.$store.commit('deleteAcpAry', {
              userID: deleteId,
              groupID: groupId
            })
          } else {
            FrameCtrl.getIns().deleteRobot(deleteId, groupId);
          }
        }).catch(err => {
            console.warn("删除机器人失败:", err);
        })
      },
      /**
       * 删除所有已选机器人
       * */
      deleteSelectedRobots() {
        this.$confirm("确定删除所有已选机器人?").then(() => {
          //确定删除
          let groupID = this.$store.state.robot.dwGroupID;
          let robotsID = [];
          this.$store.state.accompany.acyAry.forEach(robot => {
            if (robot.dwGroupID !== groupID) {
              console.warn("不正确的群组ID", robot.dwGroupID, groupID);
            }
            if (robot.isSelect === true && robot.dwGroupID === groupID) {
              robotsID.push(robot.dwUserID);
            }
          });

          robotsID.forEach(id => {
            if (this.$store.state.debug === true) {
              this.$store.commit('deleteAcpAry', {
                dwGroupID: groupID,
                dwUserID: id
              })
            } else {
              FrameCtrl.getIns().deleteRobot(id, groupID);
            }

          })

        }).catch(err => {
          //取消删除
          console.log("取消删除");
        })
      },
      _cancel(index) {
        console.log("选中:", index);
        this.btnCancel = index;
        this.$store.commit('allAcp', index);

      },
      /**
       * 添加机器人
       * */
      addConfig() {
        let err = false;
        let msg ="";
        if (this.addCount < 0) {
          err = true;
          msg = "请输入正确的数字";
        }
//        if (this.addType === 2 && this.dwParentGameID === 0) {
//          err = true;
//          msg = "请选择上级";
//        }
        if (err) {
          this.$alert(msg, "错误原因:", {
            confirmButtonText: '确定',
          }).catch(err => {
            console.log(err);
          });
          return;
        }
        if (this.$store.state.debug === true) {
            console.log("添加机器人");
        } else {
          let id = this.$store.state.robot.dwGroupID;
          let parentID = this.dwParentGameID;
          let type = this.addType;
          let count = parseInt(this.addCount);
          console.log("添加机器:", id, type, parentID, count);
          if (id === undefined || id === null) {
            console.log("添加机器GroupID出错", id);
            return
          }

          FrameCtrl.getIns().appendAndroid(id, type, parentID, count);
        }
        this._addApy();
      },
      _addApy() {
        let id = this.getGroupById(this.$store.state.robot.dwGroupID);
        if (id == null) {
          this.$confirm("请先选择需要编辑的俱乐部.", "错误", () => {

          }).catch(err => {
            console.log(err);
          });
          return
        }
        this.addApy = !this.addApy;
      },
      _close() {

      },
      onSelectType(obj) {
        this.addType = obj.type;
      },
      _onChangePages(index) {
        this.$store.commit("changeAcpIndex", index);
      },
      toggleBtn(event) {
        let index = event.type;
        //如何找到state并且改变isSelect
        //如何根据type
        let target = this.currentApy[index];
        this.$store.commit('toggleAcp', target);
      },
      onTogglePage(index) {
        this._closeAllDialog();
        let dialog = this._getDialog(index);
        dialog.openDialog();
      },
      closeDialog(index) {
        console.log("关闭页面:", index);
        let dialog = this._getDialog(index);
        dialog.closeDialog();
      },
      _getDialog(index) {
        let dialog;
        switch (index) {
          case 1 : {
            dialog = this.$refs.accomponyDialog;
            break;
          }
          case 2 : {
//            dialog = this.$refs.modifyDialog;
            break;
          }
          case 3 : {
//            dialog = this.$refs.putInDialog;
            break;
          }
        }
        return dialog;
      },
      _closeAllDialog() {
        for (let n = 1; n <= 2; n++ ) {
          this.closeDialog(n);
        }
      },
      getGenderStr(gender) {
        switch (gender) {
          case 1: {
            return "男";
          }
          case 2: {
            return "女";
          }
          default: {
            return "未知";
          }

        }
      },
      clearNoNum(e) {
        e.target.value = e.target.value.replace(/[^\d]/g,'');
        console.log(e.target.value, typeof e.target.value);
        console.log(parseInt(e.target.value));
        e.target.value = parseInt(e.target.value);
        if (parseInt(e.target.value) < 0) {
          e.target.value = 0;
        }
      },
    },

    components: {
      'my-button': MyButton,
      'nav-footer': NavFooter,
      'toggle-button': ToggleButton,
      'accompony': Accompony
    }
  }
</script>

<style   scoped type="text/stylus" lang="stylus">
  @import "./stylus/serverMag.styl";
</style>



