
function getIndex(index) {
  switch (true) {
    case index < 10 : {
      return "00" + index;
    }
    case index < 100 : {
      return "0" + index;
    }
    case index < 1000 : {
      return "" + index;
    }
  }
}
let testData = [
  {
    batchIndex: "001",
    dwBatchID: 121,
    dwGroupID: 1268881,
    dwEnterTime: 1200,
    dwLeaveTime: 1400,
    clubId: "1688888",
    nickName: "拉风大蘑菇",
    count: 10,
    serverTime: "7:00 — 10:00",
    isSelect: false
  },
  {
    batchIndex: "002",
    dwBatchID: 122,
    dwGroupID: 1268881,
    dwEnterTime: 1200,
    dwLeaveTime: 1400,
    clubId: "1688888",
    nickName: "拉风大蘑菇",
    count: 11,
    serverTime: "7:00 — 10:00",
    isSelect: false
  },
  {
    batchIndex: "003",
    dwBatchID: 123,
    dwGroupID: 1268881,
    dwEnterTime: 1200,
    dwLeaveTime: 1400,
    clubId: "1688888",
    nickName: "拉风大蘑菇",
    count: 12,
    serverTime: "7:00 — 10:00",
    isSelect: false
  },

];
let data =  [];
function timeToString(time) {
  const date = new Date(time * 1000);
  return date.toTimeString().substr(0, 8);
}
const batchStore = {
  state: {
    batchAry: data,
    batchCurrentIndex: 1
  },
  mutations: {
    setBatchAry(state, payload) {
      payload.forEach((item, index) => {
        item.count = 0;
        item.index = getIndex(index);
        item.isSelect = false;
        item.serverTime = timeToString(item.dwEnterTime) + "-" + timeToString(item.dwLeaveTime);
      });


      state.batchAry = payload;
    },
    setBatchRobotCount(state, payload) {
      let id = payload.batchID;
      let count = payload.count;
      let batchIndex = -1;
      state.batchAry.forEach((item, index) => {
        if (item.dwBatchID === id) {
          batchIndex = index;
        };
      });
      if(state.batchAry !== -1) {
        state.batchAry[batchIndex].count = count;
      }
    },
    pushBatchAry(state, payload) {
      payload.isSelect = false;
      payload.index = getIndex(state.batchAry.length);
      payload.count = 10 + state.batchAry.length;
      state.batchAry.push(payload);
    },
    deleteBatchAry(state, payload) {
      let deleteIndex = -1;
      //并且groupId也必须一致,必须重新编辑序号
      state.batchAry.forEach((batch, index) => {
        if (batch.dwBatchID === payload.dwBatchID) {
          deleteIndex = index;
        }
      });
      if (deleteIndex !== -1) {
        state.batchAry.splice(deleteIndex, 1);
      }
    },
    changeBatchIndex(state, payload) {
      //做验证，去除不符合规则的页面
      if (payload < 1 || payload > Math.ceil(state.batchAry.length / 8)) {
        console.warn("错误的页面:", payload);
        return
      }
      state.batchCurrentIndex = payload;
    },
    toggleBatch(state, payload) {
      console.warn(payload);
      let index = state.batchAry.indexOf(payload);
      if (index >=0 && index < state.batchAry.length) {
        state.batchAry[index].isSelect = !state.batchAry[index].isSelect;
      }
    },
    //全选
    allBatch(state, payload) {
      state.batchAry.forEach(acp => {
        acp.isSelect = payload;
      })
    },
    clearBatches(state) {
      state.batchAry = [];
    }
  },
  getters: {
    currentBatch(state) {
      return state.batchAry.slice((state.batchCurrentIndex - 1) * 8, (state.batchCurrentIndex) * 8);
    },
    totalBatchIndex(state) {
      return Math.ceil(state.batchAry.length / 8);
    }
  }
}
export {batchStore};



