<template>
    <!--:close-on-click-modal="false == true"-->
    <div class="modify-wrapper">
      <el-dialog
        title="批量修改属性"
        :visible.sync="onlyVisible"
        class="m-wrapper"

      >
        <div class="content-wrapper">
          <div class="line"></div>
          <div class="split" style="margin-top: 23px;">批量修改游戏</div>
          <div class="game">
            <div class="title"><span>游戏1:</span></div>
            <select v-model="gameKind1">
              <option value="0">无</option>
              <option v-for="(item, index) in $store.state.kind.kindAry" :value="item.wKindID">{{item.szKindName}}</option>
            </select >
            <div class="title"><span>游戏2:</span></div>
            <select v-model="gameKind2">
              <option value="0">无</option>
              <option v-for="(item, index) in $store.state.kind.kindAry" :value="item.wKindID">{{item.szKindName}}</option>
            </select>
            <div class="title"><span>游戏3:</span></div>
            <select v-model="gameKind3">
              <option value="0">无</option>
              <option v-for="(item, index) in $store.state.kind.kindAry" :value="item.wKindID">{{item.szKindName}}</option>
            </select>
          </div>
          <div class="split" style="margin-top: 23px;">批量修改行为</div>
          <div class="game">
            <div class="title"><span>行为模型1:</span></div>
            <select v-model="actionTemplateKind1">
              <!--根据游戏选择不同的选择模板-->
              <option value="0">无</option>
              <option v-for="(item) in getActionTemplateByID(gameKind1)" :value="item.dwTemplateID">{{item.szTemplateName}}</option>
            </select>
            <div class="title"><span>行为模型2:</span></div>
            <select v-model="actionTemplateKind2">
              <option value="0">无</option>
              <option v-for="(item) in getActionTemplateByID(gameKind2)" :value="item.dwTemplateID">{{item.szTemplateName}}</option>
            </select>
            <div class="title"><span>行为模型3:</span></div>
            <select v-model="actionTemplateKind3">
              <option value="0">无</option>
              <option v-for="(item) in getActionTemplateByID(gameKind3)" :value="item.dwTemplateID">{{item.szTemplateName}}</option>
            </select>
          </div>
          <div class="split" style="margin-top: 23px;">批量修改时间</div>
          <div class="select-wrapper">
            <div class="title"><span>上线时间:</span></div>
            <el-time-picker
              v-model="timeOnline"

              class="timeSelect"
              placeholder="任意时间点">
            </el-time-picker>
            <div class="title"><span>下线时间:</span></div>
            <el-time-picker
              v-model="timeOffline"

              class="timeSelect"
              placeholder="任意时间点">
            </el-time-picker>
          </div>
          <div class="split" style="margin-top: 23px;">批量添加加分</div>
          <div class="select-wrapper">
            <div class="title"><span>加分规则:</span></div>
            <select v-model="gameRule">
              <option value="0">无</option>
              <option v-for="(item, index) in $store.state.rule.ruleAry" :value="item.dwTemplateID">{{item.szTemplateName}}</option>
            </select>
          </div>
          <div class="btn-close">
            <my-button :commonSrc="agreeSrc" class="agree-btn" eventName="agree-event" @agree-event="modifySelectedRobot"></my-button>
            <my-button :commonSrc="closeSrc" class="close-btn" eventName="close-event" @close-event="_close"></my-button>
          </div>
        </div>
      </el-dialog>
    </div>
</template>

<script>
  import MyButton from "../myButton/MyButton.vue";
  import {mapGetters} from 'vuex';
  export default {
    data() {
      return {
        onlyVisible: this.modifyDialog,
        name: "拉风的蘑菇",
        extendSrc: "btn_extend",
        timeOnline: 86400000,
        timeOffline: 122400000,
        clubId: 123129,
        agreeSrc: "btn_agree",
        closeSrc: "btn_close",
        modifyBatches: [],
        gameKind1: 0,
        actionTemplateKind1: 0,
        gameKind2: 0,
        actionTemplateKind2: 0,
        gameKind3: 0,
        actionTemplateKind3: 0,
        gameRule: 0,
      }
    },
    methods: {
      handleClose(done) {
        done();
      },
      _close() {
        console.log("关闭");
        this.$emit("closeDialog", 2);
      },
      closeDialog() {
        this.onlyVisible = false;
      },
      openDialog() {
        this.onlyVisible = true;
      },
      setModifyBatches(batches) {
        this.modifyBatches = batches;
      },
      modifySelectedRobot() {
        let back = [];
        let type = 3;
        let kind = [this.gameKind1, this.gameKind2, this.gameKind3];
        let action = [this.actionTemplateKind1, this.actionTemplateKind2, this.actionTemplateKind3];
        for(let n = 0; n < this.modifyBatches.length; n++) {
          let id = this.modifyBatches[n].dwBatchID;
          console.log(id);
          if (id == undefined) break;
          let data = {
            dwBatchID: id,
            dwUserID: this.modifyBatches[n].dwUserID,
            dwEnterTime: this.timeOnline / 1000,
            dwLeaveTime: this.timeOffline / 1000,
            wPlayKindID: kind,
            dwSupplyTemplateID: this.gameRule,
            dwActionTemplateID: action
          };
          console.log(data);
          if (this.$store.state.debug === false ) {
            FrameCtrl.getIns().modifyBatch(id, type, data);
          } else {
            this.$store.commit('pushBatchRobotAry', data);
          }
        }
        this.$emit('open-page', 1);
      }
    },
    computed: {
      ...mapGetters(['getBatchRobotByID']),
      getActionTemplateByID() {
        let self = this;
        return id => {
          switch (id) {
            case 112: {
              return self.$store.state.niu.niuAry;
            }
            case 79: {
              return self.$store.state.san.sanAry;
            }
          }
        }
      }
    },
    watch: {
      gameKind1(newValue, oldValve) {
        this.actionTemplateKind1 = 0;
      },
      gameKind2(newValue, oldValve) {
        this.actionTemplateKind2 = 0;
      },
      gameKind3(newValue, oldValve) {
        this.actionTemplateKind3 = 0;
      },
      timeOnline(newValue, old) {
        console.log(new Date(newValue).getTime());
      }
    },
    props: {
      modifyDialog: {
        type: Boolean,
        default: false
      }
    },
    components: {
      'my-button': MyButton
    }
  }
</script>

<style  scoped type="text/stylus" lang="stylus">
  @import "./stylus/modify.styl";
</style>



