const tagTemplateA = {
  lScoreArea1: [0, 29],						//底分范围
  cbModeRate1: [],						//模式概率
  lScoreArea2: [30, 40],						//底分范围
  cbModeRate2: [],						//模式概率
  lScoreArea3: [41, 50],						//底分范围
  cbModeRate3: [],						//模式概率
  lScoreArea4: [50, 100],						//底分范围
  cbModeRate4: [],						//模式概率
  cbModeData1: [],					  //模式数据
  cbModeData2: [],					  //模式数据
  cbModeData3: [],					  //模式数据
  cbModeData4: [],
};
function getObj(obj) {
  let copy;
  //copy = {...obj};
  copy = JSON.parse(JSON.stringify(obj));
  return copy;
}
const sanGong = {
  state: {
    sanAry: [
      {
        dwTemplateID: 160,
        wGameKindID: 79,
        szTemplateName: "三公1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 161,
        wGameKindID: 79,
        szTemplateName: "赌圣1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 162,
        wGameKindID: 79,
        szTemplateName: "赌王1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 163,
        wGameKindID: 79,
        szTemplateName: "赌尊1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 164,
        wGameKindID: 79,
        szTemplateName: "赌帝1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 165,
        wGameKindID: 79,
        szTemplateName: "赌皇1",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 166,
        wGameKindID: 79,
        szTemplateName: "三公2",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 167,
        wGameKindID: 79,
        szTemplateName: "三公3",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 168,
        wGameKindID: 79,
        szTemplateName: "三公4",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
      {
        dwTemplateID: 169,
        wGameKindID: 79,
        szTemplateName: "三公5",
        szTemplateRemarks: "备注一",
        ActionTemplateData: getObj(tagTemplateA),
        isSelect: false
      },
    ],
    sanCurrentIndex: 1
  },
  mutations: {
    pushSanAry(state, payload) {
      state.sanAry = payload;
    },
    addSanAry(state, payload) {
      //判断是新增还是修改
      let sanIndex = -1;
      state.sanAry.forEach((item, index) => {
        if (item.dwTemplateID === payload.dwTemplateID) {
          sanIndex = index;
        }
      });
      if (sanIndex === -1) {
        payload.isSelect = false;
        state.sanAry.push(payload);
      } else {
        // state.niuAry.splice(niuIndex)
        payload.isSelect = false;
        state.sanAry.splice(sanIndex, 1);
        state.sanAry.splice(sanIndex, 0, payload);
        console.error("三公修改后的数据结构:", payload);
      }
      // state.niuAry.push(payload);
    },
    deleteSanModule(state, payload) {
      let deleteIndex = -1;
      state.sanAry.forEach((niu, index) => {
        if (niu.dwTemplateID === payload.dwTemplateID) {
          deleteIndex = index;
        }
      });
      if (deleteIndex !== -1) {
        state.sanAry.splice(deleteIndex, 1);
      }
    },
    changeSanIndex(state, payload) {
      //做验证，去除不符合规则的页面
      if (payload < 1 || payload > Math.ceil(state.sanAry.length / 8)) {
        console.warn("错误的页面:", payload);
        return
      }
      state.sanCurrentIndex = payload;
    },
    toggleSan(state, payload) {
      console.warn(payload);
      let index = state.sanAry.indexOf(payload);
      if (index >=0 && index < state.sanAry.length) {
        state.sanAry[index].isSelect = !state.sanAry[index].isSelect;
      }
    },
    //全选
    allSan(state, payload) {
      state.sanAry.forEach(acp => {
        acp.isSelect = payload;
      })
    },
    reverseSan(state, payload) {
      state.sanAry.forEach(acp => {
        acp.isSelect = !acp.isSelect;
      })
    }
  },
  getters: {
    currentSan(state) {
      return state.sanAry.slice((state.sanCurrentIndex - 1) * 8, (state.sanCurrentIndex) * 8);
    },
    totalSanIndex(state) {
      return Math.ceil(state.sanAry.length / 8);
    },
    getSanTemplateName(state) {
      return id => {
        let module = state.sanAry.find(item => item.dwTemplateID == id);
        if (module != null) {
          return module.szTemplateName
        }
      }
    },
    getSanTemplateByID(state) {
      return id => {
        let module = state.sanAry.find(item => item.dwTemplateID == id);
        return module
      }
    }
  }
}

export {sanGong};



